import {
    VIDEO_BOOK_DATA, IS_VIDEO_BOOK_DATA_LOADED
} from "./actionTypes";
import { sheetVideoConst } from "../../../CONSTS/sheetsHeaderConsts";
import { replaceCommasWithPeriodsInANumber } from "../../../helpers/common/replaceCommasWithPeriodsInANumber/replaceCommasWithPeriodsInANumber";

const initialState = {
    videoSheetData: [{
        "–ù–æ–º–µ—Ä –∑–∞–ø–∏—Å–∏": 0,
        "–î–∞—Ç–∞ –ø—Ä–æ–µ–∑–¥–∞": "",
        "–ö–û–î –ù–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è (—Ü–∏—Ñ—Ä–∞–º–∏)": 0,
        "–ü—É—Ç—å": 0,
        "–ö–ú": 0,
        "–ú": 0,
        "–ù–∏—Ç—å": "",
        "–ö–û–î –ó–∞–º—á–∞–Ω–∏—è (—Ü–∏—Ñ—Ä–∞–º–∏)": 0,
        "–ù–∞–∫–ª–∞–¥–∫–∞ –≤ —Å—Ç—ã–∫–µ": "",
        "–£—Å—Ç –°–∫–æ—Ä–æ—Å—Ç—å": "",
        "–û–≥—Ä. —Å–∫–æ—Ä–æ—Å—Ç–∏": "",
        "–í–µ–ª–∏—á–∏–Ω–∞ (—Ç–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä–∞, –±–µ–∑ –º–º)": 0,
        "–†–∞–¥–∏—É—Å –∫—Ä–∏–≤–æ–π": "",
        "–ü–æ–¥—Ä–µ–ª—å—Å–æ–≤–æ–µ –æ—Å–Ω–æ–≤–∞–Ω–∏–µ –¥–µ—Ä–µ–≤–æ/–±–µ—Ç–æ–Ω": "",
        "–¢–∏–ø –ø—É—Ç–∏ (–∑–≤./ –±–ø)": ""
    }],
    isVideoBookDataLoaded: false,           // –∑–∞–≥—Ä—É–∑–∏–ª–∏—Å—å –ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç
};

const videoBookDataReducer = (state = initialState, action) => {
    switch (action.type) {

        case VIDEO_BOOK_DATA: {
            const superState = {
                ...state,

                videoSheetData: action.videoBookDataObject.videoData.map(item => {
                    return {
                        "–ù–æ–º–µ—Ä –∑–∞–ø–∏—Å–∏": +item[sheetVideoConst.SEQUENTIAL_NUMBER],
                        "–î–∞—Ç–∞ –ø—Ä–æ–µ–∑–¥–∞": item[sheetVideoConst.DATE],
                        "–ö–û–î –ù–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è (—Ü–∏—Ñ—Ä–∞–º–∏)": +item[sheetVideoConst.DIRECTION_CODE],
                        "–ü—É—Ç—å": item[sheetVideoConst.TRACK],
                        "–ö–ú": +item[sheetVideoConst.KILOMETER],
                        "–ú": +item[sheetVideoConst.METER],
                        "–ù–∏—Ç—å": item[sheetVideoConst.RAIL_THREAD],
                        "–ö–û–î –ó–∞–º—á–∞–Ω–∏—è (—Ü–∏—Ñ—Ä–∞–º–∏)": +item[sheetVideoConst.RETREAT_CODE],
                        "–ù–∞–∫–ª–∞–¥–∫–∞ –≤ —Å—Ç—ã–∫–µ": item[sheetVideoConst.NUMBER_OF_HOLES_IN_LINING],
                        "–£—Å—Ç –°–∫–æ—Ä–æ—Å—Ç—å": item[sheetVideoConst.ADVENCED_SPEED],
                        "–û–≥—Ä. —Å–∫–æ—Ä–æ—Å—Ç–∏": item[sheetVideoConst.RESTRICTION_SPEED],
                        "–í–µ–ª–∏—á–∏–Ω–∞ (—Ç–æ–ª—å–∫–æ —Ü–∏—Ñ—Ä–∞, –±–µ–∑ –º–º)": +replaceCommasWithPeriodsInANumber(item[sheetVideoConst.RETREAT_AMOUNT]),
                        "–†–∞–¥–∏—É—Å –∫—Ä–∏–≤–æ–π": item[sheetVideoConst.CURVE_RADIUS],
                        "–ü–æ–¥—Ä–µ–ª—å—Å–æ–≤–æ–µ –æ—Å–Ω–æ–≤–∞–Ω–∏–µ –¥–µ—Ä–µ–≤–æ/–±–µ—Ç–æ–Ω": item[sheetVideoConst.UNDER_RAIL_BASE],
                        "–¢–∏–ø –ø—É—Ç–∏ (–∑–≤./ –±–ø)": item[sheetVideoConst.TRACK_TYPE]
                    }
                }),
            };
            return superState;
        }

        case IS_VIDEO_BOOK_DATA_LOADED: {
            const superState = {
                ...state,
                isVideoBookDataLoaded: action.isVideoBookDataLoaded
            };
            return superState;
        }

        default: return state;
    }
};

export default videoBookDataReducer;