/* –ø—Ä–∏ –≤–≤–æ–¥–µ –≤–æ –≤–∫–ª–∞–¥–∫–µ –Ω–∞—Å—Ç—Ä–æ–π–∫–∏ –Ω–æ–º–µ—Ä–∞ –≤–∞–≥–æ–Ω–∞ –∑–∞–Ω–æ—Å–∏–º –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—é –≤ —Å—Ç–µ–π—Ç */
export const setWagonFullNameActionCreator = ( wagonFullName ) => {
    return {
        type: WAGON_FULL_NAME,
        wagonFullName
    }
};

/* –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —É–∫–∞–∑—ã–≤–∞–µ—Ç —É—á–∞—Å—Ç–æ–∫ –ø—Ä–æ–≤–µ—Ä–∫–∏ –æ—Ç —Å—Ç–∞–Ω—Ü–∏–∏ –∫ —Å—Ç–∞–Ω—Ü–∏–∏ */
