/* –§—É–Ω–∫—Ü–∏—è –≤–µ—Ä–Ω–µ—Ç –Ω–æ–º–µ—Ä –ü–ß –ø–æ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º—É –µ–π –∫–æ–¥—É –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è (—Ç–∏–ø number) –∏ –∫–æ–ª–æ–º–µ—Ç—Ä—É (—Ç–∏–ø 123.123) */
import DB from "../../../DB/DB";

export function getPchNumberByCodeAndKm(code, doubleKm) {

    if (!Number.isFinite(code)) {                     // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ
        code = +code;                         // –ø—Ä–∏–≤–µ–¥–µ–º –µ–≥–æ –∫ —á–∏—Å–ª—É
    }

    if (Number.isNaN(code)) {                         // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ (–∏–ª–∏ –ø–æ—Å–ª–µ –ø–µ—Ä–≤–æ–≥–æ –ø—Ä–∏–≤–µ–¥–µ–Ω–∏—è –∫ —á–∏—Å–ª—É) NaN
        console.error("–≤ —Ñ—É–Ω–∫—Ü–∏—é –æ–ø—Ä–µ–¥–µ–ª—è—é—â—É—é –Ω–æ–º–µ—Ä –ü–ß –ø–æ –∫–æ–¥—É –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è –∏ –∫–∏–ª–æ–º–µ—Ç—Ä—É –∫–æ–¥ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è –ø–µ—Ä–µ–¥–∞–Ω –Ω–µ —á–∏—Å–ª–æ");   // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
        return "";
    }

    if (!Number.isFinite(doubleKm)) {                     // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ
        doubleKm = +doubleKm;                         // –ø—Ä–∏–≤–µ–¥–µ–º –µ–≥–æ –∫ —á–∏—Å–ª—É
    }

    if (Number.isNaN(doubleKm)) {                         // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ (–∏–ª–∏ –ø–æ—Å–ª–µ –ø–µ—Ä–≤–æ–≥–æ –ø—Ä–∏–≤–µ–¥–µ–Ω–∏—è –∫ —á–∏—Å–ª—É) NaN
        console.error("–≤ —Ñ—É–Ω–∫—Ü–∏—é –æ–ø—Ä–µ–¥–µ–ª—è—é—â—É—é –Ω–æ–º–µ—Ä –ü–ß –ø–æ –∫–æ–¥—É –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è –∏ –∫–∏–ª–æ–º–µ—Ç—Ä—É –∫–∏–ª–æ–º–µ—Ç—Ä –ø–µ—Ä–µ–¥–∞–Ω –Ω–µ —á–∏—Å–ª–æ");   // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
        return "";
    }


    const targetPch = DB.stationBoundaries.find(item =>{
        const parseDirectionCode = item.direction.match(/\d\d\d\d\d/);         // —Ä–∞—Å–ø–∞—Ä—Å–∏–º –∏–∑ —Å–≤–æ–π—Å—Ç–≤–∞ direction –≤ DB –∫–æ–¥ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è –¥–ª—è —Å—Ä–∞–≤–Ω–µ–Ω–∏—è –µ–≥–æ —Å —Ç–µ–∫—É—â–∏–º –∫–æ–¥–æ–º –≤ item
        return +parseDirectionCode === code && doubleKm > item.startCoordinate && doubleKm <= item.endCoordinate;
    });
    
    if (typeof targetPch === "undefined") {               // –µ—Å–ª–∏ –Ω–µ –Ω–∞—à–µ–ª –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è
        console.error("–í–Ω–∏–º–∞–Ω–∏–µ!!!!! –ù–æ–º–µ—Ä –ü–ß –≤ —Ñ—É–Ω–∫—Ü–∏–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω, –ø–µ—Ä–µ–ø—Ä–æ–≤–µ—Ä—å—Ç–µ —Ñ—É–Ω–∫—Ü–∏—é getPchNumberByCodeAndKm!!!!! –ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–π –∫–æ–¥ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è: " + code + ", –ø–µ—Ä–µ–¥–∞–Ω–Ω—ã–π –∫–∏–ª–æ–º–µ—Ç—Ä = " + doubleKm);
        return "";
    } else {                                    // –µ—Å–ª–∏ –Ω–∞—à–µ–ª
        return targetPch.pch;
    }
}