import {
    setWorkBook2DataActionCreator, setIsWorkBook2DataLoadingActionCreator,
    setIsWorkBook2DataLoadedActionCreator
} from "./actionCreators";


export const setWorkBook2DataThunkCreator = (worBookData) => async (dispatch) => {
    dispatch(setIsWorkBook2DataLoadingActionCreator(true));                              // –∑–∞–ø–∏—à–µ–º –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ –º—ã –∑–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ–±—ã –ø–æ–∫–∞—Ö—ã–≤–∞—Ç—å –ª–æ–∞–¥–µ—Ä

    dispatch(setWorkBook2DataActionCreator(worBookData));                                // –∑–∞–≥—Ä—É–∑–∏–º –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç

    dispatch(setIsWorkBook2DataLoadingActionCreator(false));                             // –∑–∞–ø–∏—à–µ–º –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ –º—ã —É–∂–µ –∑–∞–≥—Ä—É–∑–∏–ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ–±—ã —É–±—Ä–∞—Ç—å –ª–æ–∞–¥–µ—Ä

    dispatch(setIsWorkBook2DataLoadedActionCreator(true));                               // –¥–∞–Ω–Ω—ã–µ –∑–∞–≥—Ä—É–∂–µ–Ω—ã –≤ —Å—Ç–µ–π—Ç
}