/* –§—É–Ω–∫—Ü–∏—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –º–µ—Ç—Ä –∏ –æ–ø—Ä–µ–¥–µ–ª—è–µ—Ç –Ω–∞ –∫–∞–∫–æ–º –æ–Ω –ø–∏–∫–µ—Ç–µ */

export function definePicketByMeter(meter) {
    let pk;                                     // –ø–∏–∫–µ—Ç, –±—É–¥–µ–º –≤–æ–∑–≤—Ä–∞—â–∞—Ç—å –∏–∑ —Ñ—É–Ω–∫—Ü–∏–∏

    if (!Number.isFinite(meter)) {                     // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ
        meter = +meter;                         // –ø—Ä–∏–≤–µ–¥–µ–º –µ–≥–æ –∫ —á–∏—Å–ª—É
    }

    if (Number.isNaN(meter)) {                         // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ (–∏–ª–∏ –ø–æ—Å–ª–µ –ø–µ—Ä–≤–æ–≥–æ –ø—Ä–∏–≤–µ–¥–µ–Ω–∏—è –∫ —á–∏—Å–ª—É) NaN
        alert("–≤ —Ñ—É–Ω–∫—Ü–∏—é –æ–ø—Ä–µ–¥–µ–ª—è—é—â—É—é –ø–∏–∫–µ—Ç –ø–æ –º–µ—Ç—Ä—É –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ");   // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
    }

    const meterStr = String(meter);

    if(meterStr.length === 1 || meterStr.length === 2) {            // –µ—Å–ª–∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ü–∏—Ñ—Ä –≤ –º–µ—Ç—Ä–µ 1 –∏–ª–∏ 2, —Ç–æ –ø–∏–∫–µ—Ç –±—É–¥–µ—Ç 1
        pk = 1;
    } else if (meterStr.length === 3) {                             // –µ—Å–ª–∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ü–∏—Ñ—Ä –≤ –º–µ—Ç—Ä–µ —Ä–∞–≤–Ω–æ —Ç—Ä–∏, –Ω–∞–ø—Ä–∏–º–µ—Ä 321
        let meterFirstNumber = meterStr.substr(0, 1);               // –ø–æ–ª—É—á–∏–º –ø–µ—Ä–≤—É—é —Ü–∏—Ñ—Ä—É
        meterFirstNumber = Number(meterFirstNumber);                // –ø—Ä–∏–≤–µ–¥–µ–º –µ–µ –∫ —á–∏—Å–ª—É
        pk = meterFirstNumber + 1;                                  // –ø—Ä–∏–±–∞–≤–∏–º 1 —á—Ç–æ–±—ã –ø–æ–ª—É—á–∏—Ç –ø–∏–∫–µ—Ç
    } else if(meterStr.length > 3) {                                // –µ—Å–ª–∏ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ü–∏—Ñ—Ä –≤ –º–µ—Ç—Ä–µ –±–æ–ª–µ–µ —Ç—Ä–µ—Ö, –Ω–∞–ø—Ä–∏–º–µ—Ä 1022        
        let meterFirstNumber = meterStr.substr(0, 2);               // –ø–æ–ª—É—á–∏–º –ø–µ—Ä–≤—ã–µ 2 —Ü–∏—Ñ—Ä—ã
        meterFirstNumber = Number(meterFirstNumber);                // –ø—Ä–∏–≤–µ–¥–µ–º –∏—Ö –∫ —á–∏—Å–ª—É
        pk = meterFirstNumber + 1;                                  // –ø—Ä–∏–±–∞–≤–∏–º 1 —á—Ç–æ–±—ã –ø–æ–ª—É—á–∏—Ç –ø–∏–∫–µ—Ç
    }

    return pk;
}