import {
    WORK_BOOK_DATA, IS_WORK_BOOK_DATA_LOADED,
    IS_WORK_BOOK_DATA_LOADING, REPORT_FOR_DATE,
    MAKE_CALCULATION
} from "./actionTypes";

/* –≤ workBookDataObject –ª–µ–∂–∞—Ç –¥–∞–Ω–Ω—ã–µ –∏–∑ –ª–∏—Å—Ç–æ–≤ –û—Ç—Å—Ç—É–ø–ª–µ–Ω–∏—è –∏ –û—Ü–µ–Ω–∫–∞ –ö–ú –≤ –¥–≤—É—Ö —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏—Ö –æ–±—ä–µ–∫—Ç–∞—Ö,
—Å—Ç—Ä—É–∫—Ç—É—Ä–∞ –≤–Ω—É—Ç—Ä–∏ –∫–∞–∂–¥–æ–≥–æ –∏–∑ —ç—Ç–∏—Ö –æ–±—ä–µ–∫—Ç–æ–≤: –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤(–æ–¥–∏–Ω –æ–±—ä–µ–∫—Ç === –æ–¥–Ω–∞ —Å—Ç—Ä–æ–∫–∞ –≤ –µ–∫—Å–µ–ª—å) */
export const setWorkBookDataActionCreator = ( workBookDataObject ) => {
    return {
        type: WORK_BOOK_DATA,
        workBookDataObject
    }
};

export const setIsWorkBookDataLoadedActionCreator = ( boolVar ) => {                // –∑–∞–≥—Ä—É–∑–∏–ª–∏—Å—å –ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç
    return {
        type: IS_WORK_BOOK_DATA_LOADED,
        isWorkBookDataLoaded: boolVar
    }
};

export const setIsWorkBookDataLoadingActionCreator = ( boolVar ) => {                // –∑–∞–≥—Ä—É–∂–∞—é—Ç—Å—è –ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç –ø–æ –º–æ–º–µ–Ω—Ç—É
    return {
        type: IS_WORK_BOOK_DATA_LOADING,
        isWorkBookDataLoading: boolVar
    }
};

export const setDateActionCreator = ( day, month, year ) => {                // –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –≤–≤–æ–¥–∏—Ç –∑–∞ –∫–∞–∫–æ–π –¥–µ–Ω—å –Ω–∞–¥–æ —Å–¥–µ–ª–∞—Ç—å –æ—Ç—á–µ—Ç, –Ω—É–∂–µ–Ω –¥–ª—è —Ñ–∏–ª—å—Ç—Ä–∏ –ø—Ä–∏ —Ä–∞—Å—á–µ—Ç–∞—Ö
    return {
        type: REPORT_FOR_DATE,
        reportForDay: day,
        reportForMonth: month,
        reportForYear: year
    }
};


// –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç—å –ª–∏ —Ä–∞—Å—á–µ—Ç –≤ —Å–µ–ª–µ–∫—Ç–æ—Ä–µ, –±—É–¥–µ—Ç –ø–µ—Ä–µ–∫–ª—é—á–∞—Ç—å—Å—è –Ω–∞ true 
// –≤ –º–æ–º–µ–Ω—Ç –Ω–∞–∂–∞—Ç–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º –∫–Ω–æ–ø–∫–∏ –∑–∞–≥—Ä—É–∑–∏—Ç—å –∫–∞–∫–æ–π-–ª–∏–±–æ –æ—Ç—á–µ—Ç, 
// –∏ –æ–±—Ä–∞—Ç–Ω–æ –Ω–∞ false  –≤–º–æ–º–µ–Ω—Ç –æ–∫–æ–Ω—á–∞–Ω–∏—è –∑–∞–≥—Ä—É–∑–∫–∏ –æ—Ç—á–µ—Ç–∞
export const setMakeCalculationActionCreator = ( boolVar ) => {
    return {
        type: MAKE_CALCULATION,
        makeCalculation: boolVar
    }
};