import {
    WORK_BOOK_3_DATA, IS_WORK_BOOK_3_DATA_LOADED,
    IS_WORK_BOOK_3_DATA_LOADING,
} from "./actionTypes";

/* –≤ workBook3DataObject –ª–µ–∂–∞—Ç –¥–∞–Ω–Ω—ã–µ –∏–∑ –ª–∏—Å—Ç–æ–≤ –û—Ç—Å—Ç—É–ø–ª–µ–Ω–∏—è –∏ –û—Ü–µ–Ω–∫–∞ –ö–ú –≤ –¥–≤—É—Ö —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤—É—é—â–∏—Ö –æ–±—ä–µ–∫—Ç–∞—Ö,
—Å—Ç—Ä—É–∫—Ç—É—Ä–∞ –≤–Ω—É—Ç—Ä–∏ –∫–∞–∂–¥–æ–≥–æ –∏–∑ —ç—Ç–∏—Ö –æ–±—ä–µ–∫—Ç–æ–≤: –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤(–æ–¥–∏–Ω –æ–±—ä–µ–∫—Ç === –æ–¥–Ω–∞ —Å—Ç—Ä–æ–∫–∞ –≤ –µ–∫—Å–µ–ª—å) */
export const setWorkBook3DataActionCreator = ( workBook3DataObject ) => {
    return {
        type: WORK_BOOK_3_DATA,
        workBook3DataObject
    }
};

export const setIsWorkBook3DataLoadedActionCreator = ( boolVar ) => {                // –∑–∞–≥—Ä—É–∑–∏–ª–∏—Å—å –ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç
    return {
        type: IS_WORK_BOOK_3_DATA_LOADED,
        isWorkBook3DataLoaded: boolVar
    }
};

export const setIsWorkBook3DataLoadingActionCreator = ( boolVar ) => {                // –∑–∞–≥—Ä—É–∂–∞—é—Ç—Å—è –ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç –ø–æ –º–æ–º–µ–Ω—Ç—É
    return {
        type: IS_WORK_BOOK_3_DATA_LOADING,
        isWorkBook3DataLoading: boolVar
    }
};