/*
    –ï—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–µ —Å–æ–≥–ª–∞—Å–∏–ª—Å—è —Å "–£—Å–ª–æ–≤–∏—è–º–∏ –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è —Å–∞–π—Ç–∞" –≤–µ—Ä–Ω–µ—Ç —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± —ç—Ç–æ–º, –≤ –ø—Ä–æ—Ç–∏–≤–Ω–æ–º —Å–ª—É—á–∞–µ –≤–µ—Ä–Ω–µ—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –ø–µ—Ä–µ–¥–∞–Ω–Ω—ã–π –≤ –¥–∞–Ω–Ω—ã–π HOC
*/
import React from "react";
import { Alert } from 'antd';
import { useSelector } from 'react-redux';
import {
    selectIsUserAgreesWithTermsOfUse
} from "../state/features/termsOfUse/selectors";


export const WithTermsOfUse = (props) => {
    // -------------------------------------------------------------- –•—É–∫–∏ ---------------------------------------------------------------------------
    const isUserAgreesWithTermsOfUse = useSelector(selectIsUserAgreesWithTermsOfUse);
    // -------------------------------------------------------------- / –•—É–∫–∏ -------------------------------------------------------------------------

    if(!isUserAgreesWithTermsOfUse) {
        return (
            <Alert message="–í—ã –ù–ï —Å–æ–≥–ª–∞—Å–∏–ª–∏—Å—å —Å –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å—Å–∫–∏–º –°–æ–≥–ª–∞—à–µ–Ω–∏–µ–º. –ò—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ —ç—Ç–æ–≥–æ —Å–∞–π—Ç–∞ –Ω–µ–≤–æ–∑–º–æ–∂–Ω–æ" type="error" showIcon />
        );
    } else {
        return <props.component />
    }
}