                        "–î–õ–ö–ò–õ–û–ú–ï–¢–†–ê": item["–î–õ–ö–ò–õ–û–ú–ï–¢–†–ê"],
                        "–ö–ê–¢–ï–ì–û–†–ò–Ø": item["–ö–ê–¢–ï–ì–û–†–ò–Ø"],
                        "–ö–õ–ê–°–°": item["–ö–õ–ê–°–°"],
