                        "–ö–û–î–ù–ê–ü–†": +item["–ö–û–î–ù–ê–ü–†"],
                        "–õ–ò–ù–ò–Ø": item["–õ–ò–ù–ò–Ø"],
                        "–ú–ï–°–Ø–¶": +item["–ú–ï–°–Ø–¶"],
                        "–ù–ê–ü–†–ê–í–õ–ï–ù–ò–ï": item["–ù–ê–ü–†–ê–í–õ–ï–ù–ò–ï"],
