export function getUniqueNumbersFromArr(arr) {           // –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –º–∞—Å—Å–∏–≤, –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π
    let result = [];

    arr.forEach(element => {                               // –¥–ª—è –∫–∞–∂–¥–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –º–∞—Å—Å–∏–≤–∞
        if (!result.includes(element)) {                   // –∏ –≤ –º–∞—Å—Å–∏–≤–µ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π –µ—â–µ –Ω–µ—Ç —Ç–∞–∫–æ–≥–æ –∑–Ω–∞—á–µ–Ω–∏—è
            result.push(element);                         // –∑–∞–ø—É—à–∏–º –µ–≥–æ —Ç—É–¥–∞
        }
    });

    return result;
}