import React from "react";
import { useDispatch, useSelector } from "react-redux";
import XLSX from "xlsx/dist/xlsx.full.min";
import { Upload, Button } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import {
    selectIsWorkBook3DataLoaded
} from "../../../../state/features/workBook3Data/selectors";
import { setWorkBook3DataThunkCreator } from "../../../../state/features/workBook3Data/thunkCreators";

export const FilesUploadBook3 = () => {
    // -------------------------------------------------------------- –•—É–∫–∏ ---------------------------------------------------------------------------
    const dispatch = useDispatch();
    const isBook3DataLoaded = useSelector(selectIsWorkBook3DataLoaded);                                      // –∑–∞–≥—Ä—É–∂–Ω—ã –ª–∏ –¥–∞–Ω–Ω—ã–µ –ø–æ —Ç–µ–∫—É—â–µ–º—É –ø—Ä–æ–µ–∑–¥—É –≤ —Å—Ç–µ–π—Ç
    // -------------------------------------------------------------- / –•—É–∫–∏ -------------------------------------------------------------------------

    // ---------------------------------------- –ü—Ä–æ–ø—Å—ã –∫–æ—Ç–æ—Ä—ã–µ –±—É–¥–µ–º –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å –≤ Upload Ant Design ----------------------------------------
    const props = {
        name: 'file',
        // action: 'https://www.mocky.io/v2/5cc8019d300000980a055e76',
        // headers: {
        //     authorization: 'authorization-text',
        // },
        onChange(evt) {
            let workBookData;                                    // –≤–æ–∑–≤—Ä–∞—â–∞–µ–º json
            const selectedFile = evt.fileList[0].originFileObj;           // –≤—ã–±—Ä–∞–Ω–Ω—ã–π –≤ –±—Ä–∞—É–∑–µ—Ä–µ —Ñ–∞–π–ª, –æ–¥–∏–Ω, —Ç–∞–∫ –∫–∞–∫ –∑–∞–ø—Ä–µ—â–µ–Ω –º—É–ª—å—Ç–∏ –≤—ã–±–æ—Ä —Ñ–∞–π–ª–æ–≤

            if (selectedFile) {                                 // –µ—Å–ª–∏ —Ñ–∞–π–ª –±—ã–ª –≤—ã–±—Ä–∞–Ω. —ç—Ç–∞ –ø—Ä–æ–≤–µ—Ä–∫–∞ —á—Ç–æ–±—ã –µ—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–∞–∂–∞–ª –∫–Ω–æ–ø–∫—É –≤—ã–±—Ä–∞—Ç—å —Ñ–∞–π–ª –∞ –ø–æ—Ç–æ–º –∑–∞–∫—Ä—ã–ª –æ–∫–Ω–æ —Å –≤—ã–±–æ—Ä–æ–º —Ñ–∞–π–ª–∞ –Ω–µ –≤—ã–±—Ä–∞–≤ –µ–≥–æ
                let reader = new FileReader();
                reader.readAsBinaryString(selectedFile);
                reader.onload = function (event) {

                    const data = event.target.result;
                    const workBook = XLSX.read(data, {
                        type: 'binary'
                    });

                    const workSheetOtstDataObj = workBook.Sheets["–û—Ç—Å—Ç—É–ø–ª–µ–Ω–∏—è"];
                    const workSheetOtstDataJson = XLSX.utils.sheet_to_json(workSheetOtstDataObj);

                    const workSheetOcKmDataObj = workBook.Sheets["–û—Ü–µ–Ω–∫–∞ –ö–ú"];
                    const workSheetOcKmDataJson = XLSX.utils.sheet_to_json(workSheetOcKmDataObj);


                    workBookData = {
                        otstSheetData: workSheetOtstDataJson,
                        ocKmSheetData: workSheetOcKmDataJson
                    }

                    dispatch(setWorkBook3DataThunkCreator(workBookData));
                };

                reader.onerror = function (event) {
                    workBookData = null
                    console.error("–§–∞–π–ª –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—Ä–æ—á–∏—Ç–∞–Ω. –ö–æ–¥ –æ—à–∏–±–∫–∏: " + event.target.error.code);
                };
            }
        },
    };
    // ---------------------------------------- / –ü—Ä–æ–ø—Å—ã –∫–æ—Ç–æ—Ä—ã–µ –±—É–¥–µ–º –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å –≤ Upload Ant Design --------------------------------------


    return (
        <>
            <div className="content__input-item">
                {
                    isBook3DataLoaded
                        ? <>
                            <h2>–î–∞–Ω–Ω—ã–µ –ø–æ –ø–æ–∑–∞–ø—Ä–æ—à–ª–æ–º—É –ø–µ—Ä–∏–æ–¥—É —É—Å–ø–µ—à–Ω–æ –∑–∞–≥—Ä—É–∂–µ–Ω—ã</h2>
                        </>
                        : <>
                            <h2>–î–∞–Ω–Ω—ã–µ –ø–æ –ø–æ–∑–∞–ø—Ä–æ—à–ª–æ–º—É –ø–µ—Ä–∏–æ–¥—É –Ω–µ –∑–∞–≥—Ä—É–∂–µ–Ω—ã, —Å–Ω–∞—á–∞–ª–∞ –∑–∞–≥—Ä—É–∑–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ.
                                –ù–∞–ø—Ä–∏–º–µ—Ä –µ—Å–ª–∏ —Å–µ–π—á–∞—Å –ø–µ—Ä–∏–æ–¥ –ê–ø—Ä–µ–ª—å –∫–æ–Ω—Ç—Ä–æ–ª—å–Ω—ã–π –∑–∞–≥—Ä—É–∑–∏—Ç–µ —Å—é–¥–∞ —Ñ–∞–π–ª —Å –¥–∞–Ω–Ω—ã–º–∏ –ø–æ –ú–∞—Ä—Ç—É –∫–æ–Ω—Ç—Ä–æ–ª—å–Ω–æ–º—É</h2>
                            <Upload {...props}>
                                <Button type="primary" icon={<UploadOutlined />}>–ó–∞–≥—Ä—É–∑–∏—Ç—å —Ñ–∞–π–ª</Button>
                            </Upload>
                            {/* <input style={{ color: "green" }} type="file" onChange={(e) => onBookSelect(e)} /> */}
                        </>
                }
            </div>
        </>
    );
}