        "–ö–û–õ–ò–ß–ï–°–¢–í–û": 0,
        "–õ–ò–ù–ò–Ø": "",
        "–ú": 0,
        "–ú–ï–°–Ø–¶": 0,
        "–ú–û–°–¢": 0,
        "HOPMA": 0,
