import { createSelector } from "reselect";
import { selectWagonFullName, selectInspectionArea } from "../wagonInfo/selectors";
import { selectWorkBookOcKmSheetData, selectReportForDay } from "../workBookData/selectors";
import { getUniqueNumbersFromArr } from "../../../helpers/common/getUniqueNumbersFromArr/getUniqueNumbersFromArr";
import { getPchNumberByCodeAndKm } from "../../../helpers/common/getPchNumberByCodeAndKm/getPchNumberByCodeAndKm";
import { getRegionNumberByPchNumber } from "../../../helpers/common/getRegionNumberByPchNumber/getRegionNumberByPchNumber";
import { sheetVideoConst, sheetOcKmConst } from "../../../CONSTS/sheetsHeaderConsts";
import { getDirectionByCode } from "../../../helpers/common/getDirectionByCode/getDirectionByCode";
import { getPchFullNameByPchNumber } from "../../../helpers/common/getPchFullNameByPchNumber/getPchFullNameByPchNumber"
import DB from "../../../DB/DB";
import { retreatVideoCodes } from "../../../DB/retreatVideoCodes";



export const selectVideoBookData = (state) => {
    return state.videoBookData.videoSheetData;
}

export const selectIsVideoBookDataLoaded = (state) => {
    return state.videoBookData.isVideoBookDataLoaded;
}


// ---------------------------------------------- –†–∞—Å—á–∏—Ç–∞–µ–º –¥–∞–Ω–Ω—ã–µ –¥–ª—è —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã –ø–æ –≤–∏–¥–µ–æ  -----------------------------------------
export const selectCalculatedDataTelegramVideo = createSelector(
    [selectVideoBookData, selectWorkBookOcKmSheetData, selectWagonFullName, selectInspectionArea, selectReportForDay],
    (videoData, ocKmData, wagonFullName, inspectionArea, reportForDay) => {
        // –í–æ–∑–≤—Ä–∞—â–∞–µ–º—ã–π –æ–±—ä–µ–∫—Ç —Ä–∞—Å—á–∏—Ç–∞–Ω–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö
        let returnedDataObject = {};

        // –ú–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤ - –¥–ª—è —Ñ–æ—Ä–º–∞–∏—Ä–æ–≤–∞–Ω–∏—è AoA –≤ AoACreator`–µ
        let forAoACreatorAoO = [];

        // –ú–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ - –¥–ª—è —Ñ–æ—Ä–º–∞–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ excel –∏ —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –≤ –±—Ä–∞—É–∑–µ—Ä–µ
        let forExcelAndPageRenderingData = [];

        // ------------------------------------ –ü–µ—Ä–≤–∞—è —Å—Ç—Ä–æ–∫–∞ —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã ----------------------------------------
        // -------------- –£–Ω–∏–∫–∞–ª—å–Ω—ã–µ –ü–ß --------------
        const allPchArr = videoData.map(item => {                                                    // –º–∞—Å—Å–∏–≤ —Å –Ω–æ–º–µ—Ä–∞–º–∏ –≤—Å–µ—Ö –ü–ß –≤—ã—á–∏—Å–ª–µ–Ω–∏—è —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –ü–ß
            const kilometerMeterDouble = `${item[sheetVideoConst.KILOMETER]}.${item[sheetVideoConst.METER]}`        // –∫–∏–ª–æ–º–µ—Ç—Ä –∏ –º–µ—Ç—Ä –¥–µ—Å—è—Ç–∏—á–Ω–∞—è –¥—Ä–æ–±—å. –î–ª—è —Ñ—É–Ω–∫—Ü–∏–∏ getPchNumberByCodeAndKm
            const distanceNumber = getPchNumberByCodeAndKm(item[sheetVideoConst.DIRECTION_CODE], kilometerMeterDouble)
            return distanceNumber;
        });
        const uniquePchNumbersArr = getUniqueNumbersFromArr(allPchArr);                // –º–∞—Å—Å–∏–≤ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –Ω–æ–º–µ—Ä–æ–≤ –ü–ß (—Ü–∏—Ñ—Ä—ã)
        let uniquePchPartAndNumbersArr;
        if (uniquePchNumbersArr[0] === "") {                                            // –µ—Å–ª–∏ –Ω–µ –∑–∞–≥—Ä—É–∑–∏–ª–∏ —Ñ–∞–π–ª —Å –≤–∏–¥–µ–æ –ø—Ä–∏ –ø–µ—Ä–≤–æ–º —Ä–µ–Ω–¥–µ—Ä–∏–Ω–≥–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã, —á—Ç–æ–±—ã –Ω–µ –±—ã–ª–æ –æ—à–∏–±–∫–∏ –ø—Ä–∏–ª–æ–∂–µ–Ω–∏—è, —Ç–∞–∫ –∫–∞–∫ map –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –Ω–∞ ""
            uniquePchPartAndNumbersArr = [];
        } else {
            uniquePchPartAndNumbersArr = uniquePchNumbersArr.map(item => DB.distances.find(element => {       // –º–∞—Å—Å–∏–≤ –∏–∑ –ø—Ä–∏–Ω–∞–¥–ª–µ–∂–Ω–æ—Å—Ç–∏ –∏ –Ω–æ–º–µ—Ä–∞ –ü–ß (–Ω–∞–ø—Ä–∏–º–µ—Ä: –ü–ß-15)
                return element.distanceNumber === item;
            }).distancePartAndNumber);
        }
        const uniquePchPartAndNumbersStr = uniquePchPartAndNumbersArr.join(", ");                           // —Å—Ç—Ä–æ–∫–∞ –≤–∏–¥–∞ "–ü–ß-3, –ü–ß-33" –¥–ª—è –∞–¥—Ä–µ—Å–æ–≤ –≤ —Ç–µ–ª–µ–≥—Ä–∞–º–º–µ
        // -------------- / –£–Ω–∏–∫–∞–ª—å–Ω—ã–µ –ü–ß ------------

        // -------------- –£–Ω–∏–∫–∞–ª—å–Ω—ã–µ –†–µ–≥–∏–æ–Ω—ã --------------
        const RegionsArr = uniquePchNumbersArr.map(distanceNumber => getRegionNumberByPchNumber(DB, distanceNumber));       // –º–∞—Å—Å–∏–≤ —Å –Ω–æ–º–µ—Ä–∞–º–∏ —Ä–µ–≥–∏–æ–Ω–æ–≤ –ø–æ –∫–∞–∂–¥–æ–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏
        const uniqueRegionsArr = getUniqueNumbersFromArr(RegionsArr);                   // –º–∞—Å—Å–∏–≤ –∏–∑ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–π
        const uniqueRegionsStr = uniqueRegionsArr.join(", ");                           // // —Å—Ç—Ä–æ–∫–∞ —Å–æ—Å—Ç–æ—è—â–∞—è –∏–∑ –Ω–æ–º–µ—Ä–æ–≤ —Ä–µ–≥–∏–æ–Ω–æ–≤ –≤–∏–¥–∞ "1, 3" –¥–ª—è –∞–¥—Ä–µ—Å–æ–≤ –≤ —Ç–µ–ª–µ–≥—Ä–∞–º–º–µ

        const firstTelegramRow = [`${uniquePchPartAndNumbersStr}; –ö–æ–ø–∏—è: –ù–ó-–†–ë, –∑–∞–º –†–ë-—Ä–µ–≥-${uniqueRegionsStr}, –†–¶–î–ú, –î–ò–¶–£–°–ò.`];
        // -------------- / –£–Ω–∏–∫–∞–ª—å–Ω—ã–µ –†–µ–≥–∏–æ–Ω—ã ------------
        // ------------------------------------ / –ü–µ—Ä–≤–∞—è —Å—Ç—Ä–æ–∫–∞ —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã --------------------------------------


        // ------------------------------------ –í—Ç–æ—Ä–∞—è —Å—Ç—Ä–æ–∫–∞ —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã ----------------------------------------
        // ---------- –î–∞—Ç–∞ –ø—Ä–æ–≤–µ—Ä–∫–∏ ------------
        const currentDateFirstObj = videoData[0];
        const currentDate = currentDateFirstObj[sheetVideoConst.DATE];
        // ---------- / –î–∞—Ç–∞ –ø—Ä–æ–≤–µ—Ä–∫–∏ ----------

        // -------- –ö–æ–ª–∏—á–µ—Å—Å—Ç–≤–æ –ø—Ä–æ–≤–µ—Ä–µ–Ω–Ω—ã—Ö –∫–∏–ª–æ–º–µ—Ç—Ä–æ–≤ --------
        const totalCheckedKilometers = ocKmData.reduce((prevVal, item) => {
            if (item[sheetOcKmConst.DAY] === +reportForDay) {
                let sum = Number(prevVal) + Number(item[sheetOcKmConst.CHECKED_KILOMETERS]);
                sum = sum.toFixed(3);
                return sum;
            } else {
                return +prevVal;
            }
        }, 0);
        // -------- / –ö–æ–ª–∏—á–µ—Å—Å—Ç–≤–æ –ø—Ä–æ–≤–µ—Ä–µ–Ω–Ω—ã—Ö –∫–∏–ª–æ–º–µ—Ç—Ä–æ–≤ ------

        const secondTelegramRow = [`${currentDate} –≥. –≤–∞–≥–æ–Ω–æ–º ${wagonFullName} –ø—Ä–æ–≤–µ—Ä–µ–Ω–æ —Å–æ—Å—Ç–æ—è–Ω–∏–µ –ø—É—Ç–∏ –Ω–∞ —É—á–∞—Å—Ç–∫–µ: ${inspectionArea} –ø—Ä–æ–≤–µ—Ä–µ–Ω–æ ${totalCheckedKilometers} –∫–º –≥–ª–∞–≤–Ω—ã—Ö –ø—É—Ç–µ–π.`]
        // ------------------------------------ / –í—Ç–æ—Ä–∞—è —Å—Ç—Ä–æ–∫–∞ —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã --------------------------------------


        // ------------------------------------------------- –¢—Ä–µ—Ç—å—è —Å—Ç—Ä–æ–∫–∞ —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã ------------------------------------------------------
        // ------- –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤—ã—è–≤–ª–µ–Ω–Ω—ã—Ö –æ—Ç—Å—Ç—É–ø–ª–µ–Ω–∏–π –ø–æ –≤–∏–¥–µ–æ –±–µ–∑ –Ω—É–ª–µ–π –≤ –≥—Ä–∞—Ñ–µ –≤–µ–ª–∏—á–∏–Ω–∞, —á—Ç–æ–±—ã –æ–Ω–∏ –Ω–µ –ø–æ–ø–∞–ª–∏ –≤ —Ç–µ–ª–µ–≥—Ä–∞–º–º—É --------
        const videoRetreatsWithoutNull = videoData.reduce((prevVal, item) => {
            if (item[sheetVideoConst.RETREAT_AMOUNT] !== 0) {
                return +prevVal + 1
            } else {
                return +prevVal
            }
        }, 0);
        // ------- –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤—ã—è–≤–ª–µ–Ω–Ω—ã—Ö –æ—Ç—Å—Ç—É–ø–ª–µ–Ω–∏–π –ø–æ –≤–∏–¥–µ–æ –±–µ–∑ –Ω—É–ª–µ–π –≤ –≥—Ä–∞—Ñ–µ –≤–µ–ª–∏—á–∏–Ω–∞, —á—Ç–æ–±—ã –æ–Ω–∏ –Ω–µ –ø–æ–ø–∞–ª–∏ –≤ —Ç–µ–ª–µ–≥—Ä–∞–º–º—É --------

        const thirdTelegramRow = [`–ü–æ —Ä–µ–∑—É–ª—å—Ç–∞—Ç–∞–º —Ä–∞—Å—à–∏—Ñ—Ä–æ–≤–∫–∏ –≤–∏–¥–µ–æ–∑–∞–ø–∏—Å–∏ –≤–∞–≥–æ–Ω–∞ ${wagonFullName} –≤—ã—è–≤–ª–µ–Ω–æ ${videoRetreatsWithoutNull} –∑–∞–º–µ—á–∞–Ω–∏–π –ø–æ —Å–æ–¥–µ—Ä–∂–∞–Ω–∏—é –í–°–ü.`];
        // ------------------------------------------------- / –¢—Ä–µ—Ç—å—è —Å—Ç—Ä–æ–∫–∞ —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã ----------------------------------------------------


        // ------------------- –º–∞—Å—Å–∏–≤—ã –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ –≤ —ç–∫—Å–µ–ª—å –∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ -----------------------
        let forXLSXAoA = [firstTelegramRow, secondTelegramRow, thirdTelegramRow];
        let forBrowserPageRenderObj = [firstTelegramRow, secondTelegramRow, thirdTelegramRow];
        // ------------------- / –º–∞—Å—Å–∏–≤—ã –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ –≤ —ç–∫—Å–µ–ª—å –∏ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ ---------------------


        // ------------------------------------------------- –ß–µ—Ç–≤–µ—Ä—Ç–∞—è —Å—Ç—Ä–æ–∫–∞ —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã ------------------------------------------------------
        let directionCode = 0;
        let track = 0;
        let distanceNumberFromMemory = 0;       // –ø–æ—Å–ª–µ–¥–Ω–∏–π —Å–æ—Ö—Ä–∞–Ω–Ω–µ–Ω—ã–π –Ω–æ–º–µ—Ä –¥–∏—Å—Ç–∞–Ω—Ü–∏–∏ –ø—É—Ç–∏
        
        videoData.forEach(item => {
            const kilometerMeterDouble = `${item[sheetVideoConst.KILOMETER]}.${item[sheetVideoConst.METER]}`;        // –∫–∏–ª–æ–º–µ—Ç—Ä –∏ –º–µ—Ç—Ä –¥–µ—Å—è—Ç–∏—á–Ω–∞—è –¥—Ä–æ–±—å. –î–ª—è —Ñ—É–Ω–∫—Ü–∏–∏ getPchNumberByCodeAndKm
            const distanceNumber = getPchNumberByCodeAndKm(item[sheetVideoConst.DIRECTION_CODE], kilometerMeterDouble); // –Ω–æ–º–µ—Ä –¥–∏—Å—Ç–∞–Ω—Ü–∏–∏ –ø—É—Ç–∏ –≤—ã—á–∏—Å–ª–µ–Ω–Ω—ã–π –Ω–∞ –æ—Å–Ω–æ–≤–µ –¥–∞–Ω–Ω—ã—Ö –∏–∑ –∑–∞–ø–æ–Ω–µ–Ω–Ω–æ–π –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º —Ç–∞–±–ª–∏—Ü—ã     
            const distanceFullName = getPchFullNameByPchNumber(DB, +distanceNumber);
            
            // –µ—Å–ª–∏ —Å–º–µ–Ω–∏—Ç—Å—è –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –∏–ª–∏ –Ω–æ–º–µ—Ä –ø—á –∏–ª–∏ –ø—É—Ç—å, –∑–∞–ø—É—à–∏–º —ç—Ç–æ –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤ —Ç–µ–ª–µ–≥—Ä–∞–º–º–µ –Ω–æ–≤–æ–π —Å—Ç—Ä–æ–∫–∏ —Å–æ —Å–º–µ–Ω–Ω–µ–Ω–Ω—ã–º –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ–º –∏–ª–∏ –ø—á –∏–ª–∏ –ø—É—Ç–µ–º
            if (directionCode !== item[sheetVideoConst.DIRECTION_CODE] || track != item[sheetVideoConst.TRACK] || distanceNumberFromMemory !== distanceNumber) {                // –µ—Å–ª–∏ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –∏–ª–∏ –ø—É—Ç—å –∏–ª–∏ –ü–ß –º–µ–Ω—è–µ—Ç—Å—è –ø—É—à–∏–º –Ω–æ–≤–æ–µ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ –≤ –º–∞—Å—Å–∏–≤
                directionCode = item[sheetVideoConst.DIRECTION_CODE];
                track = item[sheetVideoConst.TRACK];
                distanceNumberFromMemory = distanceNumber;
                
                forXLSXAoA.push([""]);                                         // –∑–∞–ø—É—à–∏–º —Ç–∞–∫–∂–µ —Ä–∞–∑—Ä—ã–≤ —Å—Ç—Ä–æ–∫–∏
                forXLSXAoA.push([`${distanceFullName}, –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ: ${getDirectionByCode(DB, directionCode)}, ${track} –ø—É—Ç—å:`]);
            }

            const retreatTitleObj = retreatVideoCodes.find(element => element.retreatCode === item[sheetVideoConst.RETREAT_CODE]);
            const retreatAmaunt = item[sheetVideoConst.RETREAT_AMOUNT] ? item[sheetVideoConst.RETREAT_AMOUNT] : "";

            if (item[sheetVideoConst.RETREAT_CODE] === 3 || item[sheetVideoConst.RETREAT_CODE] === 4 || item[sheetVideoConst.RETREAT_CODE] === 7) {     // –µ—Å–ª–∏ —ç—Ç–æ –∑–∞–∑–æ—Ä –∏–ª–∏ –ø–æ–¥–≤–∏–∂–∫–∞
                if (item[sheetVideoConst.RETREAT_AMOUNT] !== 0) {                                                                                       // –µ—Å–ª–∏ –æ–Ω–∏ –Ω–µ –Ω—É–ª–µ–≤—ã–µ
                    forXLSXAoA.push([`
                        ${item[sheetVideoConst.KILOMETER]} –∫–º ${item[sheetVideoConst.METER]} –º, ${item[sheetVideoConst.RAIL_THREAD]}, 
                        ${retreatTitleObj.retreatFullTitle} ${retreatAmaunt} –º–º.
                    `]);
                }
            } else {                                                                                                                                    // –µ—Å–ª–∏ —ç—Ç–æ –Ω–µ –∑–∞–∑–æ—Ä –∏ –Ω–µ –ø–æ–¥–≤–∏–∂–∫–∞ –∑–∞–ø—É—à–∏–º –≤ –ª—é–±–æ–º —Å–ª—É—á–∞–µ
                forXLSXAoA.push([`
                    ${item[sheetVideoConst.KILOMETER]} –∫–º ${item[sheetVideoConst.METER]} –º, ${item[sheetVideoConst.RAIL_THREAD]}, 
                    ${retreatTitleObj.retreatFullTitle}${retreatAmaunt ? retreatAmaunt + " –º–º." : "."}
                `]);
            }
        });

        forBrowserPageRenderObj = [...forXLSXAoA];
        

        // ------------------------------------------------- / –ß–µ—Ç–≤–µ—Ä—Ç–∞—è —Å—Ç—Ä–æ–∫–∞ —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã ----------------------------------------------------





        
        debugger



        // forExcelAndPageRenderingData = speedRestrictionsAoACreator(forAoACreatorAoO);

        // ------------------ –ó–∞–ø–∏—à–µ–º —Å–æ–±—Ä–∞–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –≤ –æ–±—ä–µ–∫—Ç ----------------------
        // returnedDataObject.AoO = forAoACreatorAoO;
        returnedDataObject.forXLSXAoA = forXLSXAoA
        returnedDataObject.forBrowserPageRenderObj = forBrowserPageRenderObj
        // ------------------ / –ó–∞–ø–∏—à–µ–º —Å–æ–±—Ä–∞–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –≤ –æ–±—ä–µ–∫—Ç --------------------

        return returnedDataObject;
    }
);
// ---------------------------------------------- / –†–∞—Å—á–∏—Ç–∞–µ–º –¥–∞–Ω–Ω—ã–µ –¥–ª—è —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã –ø–æ –≤–∏–¥–µ–æ  ---------------------------------------