/* –§—É–Ω–∫—Ü–∏—è –≤–µ—Ä–Ω–µ—Ç —Å–æ–∫—Ä–∞—â–µ–Ω–Ω–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –¥–∏—Ä–µ–∫—Ü–∏–∏ –∏–Ω—Ñ—Ä–∞—Å—Ç—Ä—É–∫—Ç—É—Ä—ã (–Ω–∞–ø—Ä–∏–º–µ—Ä –°–ö–ê–í) –ø–æ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º—É –µ–π –Ω–æ–º–µ—Ä—É –î–∏—Ä–µ–∫—Ü–∏–∏ (—Ç–∏–ø number) */
/* DB - –±–∞–∑–∞ –¥–∞–Ω–Ω—ã—Ö –≤ –∫–æ—Ç–æ—Ä–æ–π –∏—â–µ–º */

export function getDirectorateOfInfrastructureShortNameBydirectorateOfInfrastructureNumber(DB, directorateNumber) {

    if (!Number.isFinite(directorateNumber)) {                     // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ
        directorateNumber = +directorateNumber;                         // –ø—Ä–∏–≤–µ–¥–µ–º –µ–≥–æ –∫ —á–∏—Å–ª—É
    }

    if (Number.isNaN(directorateNumber)) {                         // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ (–∏–ª–∏ –ø–æ—Å–ª–µ –ø–µ—Ä–≤–æ–≥–æ –ø—Ä–∏–≤–µ–¥–µ–Ω–∏—è –∫ —á–∏—Å–ª—É) NaN
        console.error("–≤ —Ñ—É–Ω–∫—Ü–∏—é –æ–ø—Ä–µ–¥–µ–ª—è—é—â—É—é —Å–æ–∫—Ä–∞—â–µ–Ω–Ω–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –¥–∏—Ä–µ–∫—Ü–∏–∏ –∏–Ω—Ñ—Ä–∞—Å—Ç—Ä—É–∫—Ç—É—Ä—ã (–Ω–∞–ø—Ä–∏–º–µ—Ä –°–ö–ê–í) –ø–æ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º—É –µ–π –Ω–æ–º–µ—Ä—É –î–∏—Ä–µ–∫—Ü–∏–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ");   // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
        return "";
    }

    const directorateObjFromDB = DB.directoratesOfInfrastructure.find(item =>{
        return directorateNumber === item.directorateNumber;
    });

    if (typeof directorateObjFromDB === "undefined") {               // –µ—Å–ª–∏ –Ω–µ –Ω–∞—à–µ–ª –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è
        console.error(`–í–Ω–∏–º–∞–Ω–∏–µ!!!!! –°–æ–∫—Ä–∞—â–µ–Ω–Ω–æ–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –¥–∏—Ä–µ–∫—Ü–∏–∏ –∏–Ω—Ñ—Ä–∞—Å—Ç—Ä—É–∫—Ç—É—Ä—ã (–Ω–∞–ø—Ä–∏–º–µ—Ä –°–ö–ê–í) –≤ —Ñ—É–Ω–∫—Ü–∏–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω, –ø–µ—Ä–µ–ø—Ä–æ–≤–µ—Ä—å—Ç–µ —Ñ—É–Ω–∫—Ü–∏—é getDirectorateOfInfrastructureNameBydirectorateOfInfrastructureNumber!!!!! –ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–π –Ω–æ–º–µ—Ä –¥–∏—Ä–µ–∫—Ü–∏–∏: ${directorateNumber}`);
        return "";
    } else {                                    // –µ—Å–ª–∏ –Ω–∞—à–µ–ª
        return directorateObjFromDB.directorateShortName;
    }
}