import React from "react";
import { useDispatch, useSelector } from "react-redux";
import XLSX from "xlsx/dist/xlsx.full.min";
import { Upload, Button } from 'antd';
import { UploadOutlined } from '@ant-design/icons';
import {
    selectIsVideoBookDataLoaded
} from "../../../../state/features/videoBookData/selectors";
import { setVideoBookDataThunkCreator } from "../../../../state/features/videoBookData/thunkCreators";

// –≠—Ç–æ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –∫–æ—Ç–æ—Ä—ã–π –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –æ—Ç —é–∑–µ—Ä–∞ —Ñ–∞–π–ª "–í–∏–¥–µ–æ" –∑–∞–ø–æ–ª–Ω–µ–Ω—ã–π —é–∑–µ—Ä–æ–º. –ù–∞ –æ—Å–Ω–æ–≤–µ —ç—Ç–æ–≥–æ —Ñ–∞–π–ª–∞ —Å—Ç—Ä–æ—è—Ç—Å—è –≤—Å–µ –æ—Ç—á–µ—Ç–Ω—ã–µ —Ç–∞–±–ª–∏—Ü—ã –ø–æ –≤–∏–¥–µ–æ –∏ —Ç–µ–ª–µ–≥—Ä–∞–º–º—ã
export const FilledByUserBook = () => {
    // -------------------------------------------------------------- –•—É–∫–∏ ---------------------------------------------------------------------------
    const dispatch = useDispatch();
    const isFilledByUserBookLoaded = useSelector(selectIsVideoBookDataLoaded);                                      // –∑–∞–≥—Ä—É–∂–Ω—ã –ª–∏ –¥–∞–Ω–Ω—ã–µ –ø–æ —Ç–µ–∫—É—â–µ–º—É –ø—Ä–æ–µ–∑–¥—É –≤ —Å—Ç–µ–π—Ç
    // -------------------------------------------------------------- / –•—É–∫–∏ -------------------------------------------------------------------------



    // ------------ –ü—Ä–æ–ø—Å—ã –∫–æ—Ç–æ—Ä—ã–µ –±—É–¥–µ–º –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å –≤ Upload Ant Design, —Ç—É—Ç –∏ –º–µ—Ç–æ–¥ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –∞–ø–ª–æ–∞–¥–∞, —Ç–æ –µ—Å—Ç—å –∑–∞–≥—Ä—É–∑–∫–∏ —Ñ–∞–π–ª–∞ ------------
    const props = {
        // name: 'file',
        // action: null,
        // headers: {
        //     authorization: 'authorization-text',
        // },
        onChange(evt) {
            let workBookData;                                    // –≤–æ–∑–≤—Ä–∞—â–∞–µ–º json
            const selectedFile = evt.fileList[0].originFileObj;           // –≤—ã–±—Ä–∞–Ω–Ω—ã–π –≤ –±—Ä–∞—É–∑–µ—Ä–µ —Ñ–∞–π–ª, –æ–¥–∏–Ω, —Ç–∞–∫ –∫–∞–∫ –∑–∞–ø—Ä–µ—â–µ–Ω –º—É–ª—å—Ç–∏ –≤—ã–±–æ—Ä —Ñ–∞–π–ª–æ–≤

            if (selectedFile) {                                 // –µ—Å–ª–∏ —Ñ–∞–π–ª –±—ã–ª –≤—ã–±—Ä–∞–Ω. —ç—Ç–∞ –ø—Ä–æ–≤–µ—Ä–∫–∞ —á—Ç–æ–±—ã –µ—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –Ω–∞–∂–∞–ª –∫–Ω–æ–ø–∫—É –≤—ã–±—Ä–∞—Ç—å —Ñ–∞–π–ª –∞ –ø–æ—Ç–æ–º –∑–∞–∫—Ä—ã–ª –æ–∫–Ω–æ —Å –≤—ã–±–æ—Ä–æ–º —Ñ–∞–π–ª–∞ –Ω–µ –≤—ã–±—Ä–∞–≤ –µ–≥–æ
                let reader = new FileReader();
                reader.readAsBinaryString(selectedFile);
                reader.onload = function (event) {

                    const data = event.target.result;
                    const workBook = XLSX.read(data, {
                        type: 'binary'
                    });

                    const videoDataObj = workBook.Sheets["–í–≤–æ–¥"];
                    const videoDataObjJson = XLSX.utils.sheet_to_json(videoDataObj);


                    workBookData = {
                        videoData: videoDataObjJson
                    }

                    dispatch(setVideoBookDataThunkCreator(workBookData));
                };

                reader.onerror = function (event) {
                    workBookData = null
                    console.error("–§–∞–π–ª –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –ø—Ä–æ—á–∏—Ç–∞–Ω. –ö–æ–¥ –æ—à–∏–±–∫–∏: " + event.target.error.code);
                };
            }
        },
    };
    // ------------ / –ü—Ä–æ–ø—Å—ã –∫–æ—Ç–æ—Ä—ã–µ –±—É–¥–µ–º –ø–µ—Ä–µ–¥–∞–≤–∞—Ç—å –≤ Upload Ant Design, —Ç—É—Ç –∏ –º–µ—Ç–æ–¥ –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ –∞–ø–ª–æ–∞–¥–∞, —Ç–æ –µ—Å—Ç—å –∑–∞–≥—Ä—É–∑–∫–∏ —Ñ–∞–π–ª–∞ ----------



    return (
        <>
            <div className="content__input-item">
                {
                    isFilledByUserBookLoaded
                        ? <>
                            <h2>–î–∞–Ω–Ω—ã–µ –ø–æ –≤–∏–¥–µ–æ —É—Å–ø–µ—à–Ω–æ –∑–∞–≥—Ä—É–∂–µ–Ω—ã</h2>
                        </>
                        : <>
                            <h2>–î–∞–Ω–Ω—ã–µ –ø–æ –≤–∏–¥–µ–æ –Ω–µ –∑–∞–≥—Ä—É–∂–µ–Ω—ã, —Å–Ω–∞—á–∞–ª–∞ –∑–∞–≥—Ä—É–∑–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ</h2>
                            <Upload {...props}>
                                <Button type="primary" icon={<UploadOutlined />}>–ó–∞–≥—Ä—É–∑–∏—Ç—å —Ñ–∞–π–ª</Button>
                            </Upload>
                            {/* <input style={{ color: "green" }} type="file" onChange={(e) => onBookSelect(e)} /> */}
                            {/* <Button type="primary" icon={<UploadOutlined />} htmlType="file">–ó–∞–≥—Ä—É–∑–∏—Ç—å —Ñ–∞–π–ª</Button> */}
                        </>
                }
            </div>
        </>
    );
}