/* –§—É–Ω–∫—Ü–∏—è –≤–µ—Ä–Ω–µ—Ç –Ω–æ–º–µ—Ä –†–µ–≥–∏–æ–Ω–∞ –ø–æ –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–º—É –µ–π –Ω–æ–º–µ—Ä—É –ü–ß (—Ç–∏–ø number) */
/* DB - –±–∞–∑–∞ –¥–∞–Ω–Ω—ã—Ö –≤ –∫–æ—Ç–æ—Ä–æ–π –∏—â–µ–º */

export function getRegionNumberByPchNumber(DB, distanceNumber) {

    if (!Number.isFinite(distanceNumber)) {                     // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ
        distanceNumber = +distanceNumber;                         // –ø—Ä–∏–≤–µ–¥–µ–º –µ–≥–æ –∫ —á–∏—Å–ª—É
    }

    if (Number.isNaN(distanceNumber)) {                         // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ (–∏–ª–∏ –ø–æ—Å–ª–µ –ø–µ—Ä–≤–æ–≥–æ –ø—Ä–∏–≤–µ–¥–µ–Ω–∏—è –∫ —á–∏—Å–ª—É) NaN
        console.error("–≤ —Ñ—É–Ω–∫—Ü–∏—é –æ–ø—Ä–µ–¥–µ–ª—è—é—â—É—é –Ω–æ–º–µ—Ä –†–µ–≥–∏–æ–Ω–∞ –ø–æ –Ω–æ–º–µ—Ä—É –ü–ß –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ");   // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
        return "";
    }

    const targetRegionNumber = DB.distances.find(item =>{
        return distanceNumber === item.distanceNumber;
    });
    
    if (typeof targetRegionNumber === "undefined") {               // –µ—Å–ª–∏ –Ω–µ –Ω–∞—à–µ–ª –ø–µ—Ä–µ–¥–∞–Ω–Ω–æ–≥–æ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è
        console.error(`–í–Ω–∏–º–∞–Ω–∏–µ!!!!! –ù–æ–º–µ—Ä –†–µ–≥–∏–æ–Ω–∞ –≤ —Ñ—É–Ω–∫—Ü–∏–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω, –ø–µ—Ä–µ–ø—Ä–æ–≤–µ—Ä—å—Ç–µ —Ñ—É–Ω–∫—Ü–∏—é getRegionNumberByPchNumber!!!!! –ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–π –Ω–æ–º–µ—Ä –ü–ß: ${distanceNumber}`);
        return "";
    } else {                                    // –µ—Å–ª–∏ –Ω–∞—à–µ–ª
        return targetRegionNumber.regionNumber;
    }
}