export function getUniquePch(arrOfObjects, reportForDay) {           // –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤, –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –ü–ß
    let result = [];

    arrOfObjects.forEach(element => {                               // –¥–ª—è –∫–∞–∂–¥–æ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞ –º–∞—Å—Å–∏–≤–∞
        if(+element["–î–ï–ù–¨"] === +reportForDay) {                    // –µ—Å–ª–∏ –¥–µ–Ω—å –∫–æ—Ç–æ—Ä—ã–π –≤–≤–µ–ª –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Ä–∞–≤–µ–Ω –¥–Ω—é –≤ —ç–ª–µ–º–µ–Ω—Ç–µ
            if(!result.includes(element["–ü–ß"])) {                   // –∏ –≤ –º–∞—Å—Å–∏–≤–µ —É–Ω–∏–∫–∞–ª—å–Ω—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π –µ—â–µ –Ω–µ—Ç —Ç–∞–∫–æ–≥–æ –ü–ß
                result.push(element["–ü–ß"]);                         // –∑–∞–ø—É—à–∏–º –µ–≥–æ —Ç—É–¥–∞
            }
        }
    });

    return result;
}