import {
    setWorkBookDataActionCreator, setIsWorkBookDataLoadingActionCreator,
    setIsWorkBookDataLoadedActionCreator
} from "./actionCreators";


export const setWorkBookDataThunkCreator = (worBookData) => async (dispatch) => {
    dispatch(setIsWorkBookDataLoadingActionCreator(true));                              // –∑–∞–ø–∏—à–µ–º –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ –º—ã –∑–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ–±—ã –ø–æ–∫–∞—Ö—ã–≤–∞—Ç—å –ª–æ–∞–¥–µ—Ä

    dispatch(setWorkBookDataActionCreator(worBookData));                                // –∑–∞–≥—Ä—É–∑–∏–º –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç

    dispatch(setIsWorkBookDataLoadingActionCreator(false));                             // –∑–∞–ø–∏—à–µ–º –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ –º—ã —É–∂–µ –∑–∞–≥—Ä—É–∑–∏–ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ–±—ã —É–±—Ä–∞—Ç—å –ª–æ–∞–¥–µ—Ä

    dispatch(setIsWorkBookDataLoadedActionCreator(true));                               // –¥–∞–Ω–Ω—ã–µ –∑–∞–≥—Ä—É–∂–µ–Ω—ã –≤ —Å—Ç–µ–π—Ç
}