            <p>4.1.1. –ò–∑–º–µ–Ω—è—Ç—å –ø—Ä–∞–≤–∏–ª–∞ –ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è –°–∞–π—Ç–æ–º, –∞ —Ç–∞–∫–∂–µ –∏–∑–º–µ–Ω—è—Ç—å —Å–æ–¥–µ—Ä–∂–∞–Ω–∏–µ –¥–∞–Ω–Ω–æ–≥–æ –°–∞–π—Ç–∞.
                –ò–∑–º–µ–Ω–µ–Ω–∏—è –≤—Å—Ç—É–ø–∞—é—Ç –≤ —Å–∏–ª—É —Å –º–æ–º–µ–Ω—Ç–∞ –ø—É–±–ª–∏–∫–∞—Ü–∏–∏ –Ω–æ–≤–æ–π —Ä–µ–¥–∞–∫—Ü–∏–∏ –°–æ–≥–ª–∞—à–µ–Ω–∏—è –Ω–∞ –°–∞–π—Ç–µ.</p>
