            <p>9.2. –û—Ç–∑—ã–≤—ã –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è, —Ä–∞–∑–º–µ—â–µ–Ω–Ω—ã–µ –Ω–∞ –°–∞–π—Ç–µ, –Ω–µ —è–≤–ª—è—é—Ç—Å—è –∫–æ–Ω—Ñ–∏–¥–µ–Ω—Ü–∏–∞–ª—å–Ω–æ–π –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π –∏
            –º–æ–≥—É—Ç –±—ã—Ç—å –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω—ã –ê–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ü–∏–µ–π —Å–∞–π—Ç–∞ –±–µ–∑ –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–π.</p>

