import { DatePicker } from "antd";
import React from "react";
import moment from 'moment';
import {
    selectReportForDay, selectReportForMonth,
    selectReportForYear
} from "../../../../state/features/workBookData/selectors";
import { useDispatch, useSelector } from "react-redux";
import { setDateActionCreator } from "../../../../state/features/workBookData/actionCreators";

export const GeneralSettingsDatePicker = () => {
    // -------------------------------------------------------------- –•—É–∫–∏ ---------------------------------------------------------------------------
    const dispatch = useDispatch();
    const reportForDay = useSelector(selectReportForDay);
    const reportForMonth = useSelector(selectReportForMonth);
    const reportForYear = useSelector(selectReportForYear);
    // -------------------------------------------------------------- / –•—É–∫–∏ -------------------------------------------------------------------------

    // ------------------------------------------ –ï—Å–ª–∏ –¥–∞—Ç–∞ –≤ —Å—Ç–µ–π—Ç–µ –∑–ø–∏—Å–∞–Ω–∞ –≤–µ—Ä–Ω–µ–º –µ–µ –∏–∑ —ç—Ç–æ–π —Ñ—É–Ω–∫—Ü–∏–∏ –≤ –Ω—É–∂–Ω–æ–º —Ñ–æ—Ä–º–∞—Ç–µ ------------------------------
    const dateFromState = () => {
        const day = +reportForDay < 10 ? `0${reportForDay}` : String(reportForDay);
        const month = +reportForMonth < 10 ? `0${reportForMonth}` : String(reportForMonth);
        return `${reportForYear}-${month}-${day}`;
    }
    // ------------------------------------------ / –ï—Å–ª–∏ –¥–∞—Ç–∞ –≤ —Å—Ç–µ–π—Ç–µ –∑–ø–∏—Å–∞–Ω–∞ –≤–µ—Ä–Ω–µ–º –µ–µ –∏–∑ —ç—Ç–æ–π —Ñ—É–Ω–∫—Ü–∏–∏ –≤ –Ω—É–∂–Ω–æ–º —Ñ–æ—Ä–º–∞—Ç–µ ----------------------------


    // ------------------------------------ Declare —Ñ—É–Ω–∫—Ü–∏—é –≤—ã–∑—ã–≤–∞—é—â—É—é—Å—è –ø—Ä–∏ –≤–≤–æ–¥–µ –¥–Ω—è –≤ –ø–æ–ª–µ, –∑–∞–ø–∏—Å—ã–≤–∞–µ—Ç –≤–≤–µ–¥–µ–Ω–Ω—É—é –¥–∞—Ç—É –≤ —Å—Ç–µ–π—Ç   ------------------------------------------------
    const onDatePickerChange = (day, month, year) => {
        // if (inputFieldDayValidateErrorText !== "") {                                  // –µ—Å–ª–∏ –µ—Å—Ç—å —Ç–µ–∫—Å—Ç —Å–æ—Ö—Ä–∞–Ω–Ω–µ–Ω–Ω–æ–π –æ—à–∏–±–∫–∏ –≤–∞–ª–∏–¥–∞—Ü–∏–∏
        //     setInputFieldDayValidateErrorText("");                                      // —Å–∫–∏–Ω–µ–º –æ—à–∏–±–∫—É –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —á—Ç–æ–±—ã –æ–Ω–∞ –Ω–µ –æ—Ç–æ–±—Ä–∞–∂–∞–ª–∞—Å—å –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ
        // }
        dispatch(setDateActionCreator(day, month + 1, year));                   // –∑–∞–ø–∏—à–µ–º –¥–∞—Ç—É –∑–∞ –∫–æ—Ç–æ—Ä—É—é –Ω—É–∂–Ω–æ —Å–¥–µ–ª–∞—Ç—å –æ—Ç—á–µ—Ç –≤ —Å—Ç–µ–π—Ç
    }
    // ------------------------------------ / Declare —Ñ—É–Ω–∫—Ü–∏—é –≤—ã–∑—ã–≤–∞—é—â—É—é—Å—è –ø—Ä–∏ –≤–≤–æ–¥–µ –¥–Ω—è –≤ –ø–æ–ª–µ, –∑–∞–ø–∏—Å—ã–≤–∞–µ—Ç –≤–≤–µ–¥–µ–Ω–Ω—É—é –¥–∞—Ç—É –≤ —Å—Ç–µ–π—Ç  ---------------------------------------------


    return (
        <>
            <div className="content__input-item">
                <div>–í—ã–±–µ—Ä–∏—Ç–µ –¥–∞—Ç—É –¥–ª—è –æ—Ç—á–µ—Ç–∞</div>
                <DatePicker placeholder={"–í—ã–±–µ—Ä–∏—Ç–µ –î–∞—Ç—É"}
                    defaultValue={reportForDay === "" ? null : moment(dateFromState(), 'YYYY-MM-DD')}               // –µ—Å–ª–∏ –¥–∞—Ç–∞ –≤ —Å—Ç–µ–π—Ç–µ –µ—Å—Ç—å –ø–æ—Å—Ç–∞–≤–∏–º –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –µ–µ 
                    onChange={(e) => onDatePickerChange(e._d.getDate(), e._d.getMonth(), e._d.getFullYear())}
                />
            </div>
        </>
    );
}