/* —Ñ—É–Ω–∫—Ü–∏—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤
  –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ –∫–æ—Ä–æ—Ç–∫–∏—Ö —Ä–∏—Ö—Ç–æ–≤–æ–∫ –≤ —Ç–∞–±–ª–∏—Ü–µ –µ–¥–∏–Ω—ã—Ö —Ñ–æ—Ä–º
*/

export function shortStraighteningsAoACreator(data) {
  // –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—ã–π –æ–±—ä–µ–∫—Ç, —Ç—É—Ç –±—É–¥–µ—Ç 1 –º–∞—Å—Å–∏–≤ –∏ 1 –æ–±—ä–µ–∫—Ç:
  // 1 –º–∞—Å—Å–∏–≤ - –º–∞—Å—Å–æ–≤ –º–∞—Å—Å–≤–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ excel —Å –ø–æ–º–æ—â—å—é –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ XLSX;
  // 2 –æ–±—ä–µ–∫—Ç - –æ–±—ä–µ–∫—Ç –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤ –±—Ä–∞—É–∑–µ—Ä–µ, —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ 2 —Å–≤–æ–π—Å—Ç–≤:
      // 1 —Å–≤–æ–π—Å—Ç–≤–æ - –º–∞—Å—Å–∏–≤ –∏–∑ —ç–ª–µ–º–Ω—Ç–æ–≤ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è header`–∞ —Ç–±–∞–ª–∏—Ü–∞,
      // 2 —Å–≤–æ–π—Å—Ç–≤–æ –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è —Ç–µ–ª–∞ —Ç–∞–±–ª–∏—Ü—ã.
  let returnedObj = {};

  // –º–∞—Å—Å–æ–≤ –º–∞—Å—Å–≤–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ excel —Å –ø–æ–º–æ—â—å—é –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ XLSX;
  let forXLSXAoA = [];

  // –æ–±—ä–µ–∫—Ç –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤ –±—Ä–∞—É–∑–µ—Ä–µ, —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ 2 —Å–≤–æ–π—Å—Ç–≤:
  let forBrowserPageRenderObj = {
    header: [],         // 1 —Å–≤–æ–π—Å—Ç–≤–æ - –º–∞—Å—Å–∏–≤ –∏–∑ —ç–ª–µ–º–Ω—Ç–æ–≤ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è header`–∞ —Ç–±–∞–ª–∏—Ü–∞,
    body: []            // 2 —Å–≤–æ–π—Å—Ç–≤–æ –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è —Ç–µ–ª–∞ —Ç–∞–±–ª–∏—Ü—ã.
  };

  // –®–∞–ø–∫–∞ —Ç–∞–±–ª–∏—Ü—ã
  forXLSXAoA.push([
    "‚Ññ", "–î–∞—Ç–∞ –ø—Ä–æ–≤–µ—Ä–∫–∏", "–ü–°", "–ü–ï–†–ï–ì–û–ù, –°–¢–ê–ù–¶–ò–Ø", "–ü–ß",
    "–ü–£–¢–¨, —Å—ä–µ–∑–¥", "–ö–ú", "–ü–ö/–º", "–°—Ç–µ–ø–µ–Ω—å —Ä–∏—Ö—Ç–æ–≤–∫–∏ (—à—Ç)", "–í–µ–ª–∏—á –æ—Ç—Å—Ç—É–ø –º–º/–º",
    "–ò–∑–º–µ–Ω–µ–Ω–∏–µ –≤–µ–ª–∏—á–∏–Ω—ã —Ä–∏—Ö—Ç–æ–≤–∫–∏", "–£—Å—Ç–∞–Ω. –°–∫–æ—Ä–æ—Å—Ç—å", "–û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏—è –°–ö–û–†–û–°–¢–¨ –ø–∞—Å—Å/–≥—Ä—É–∑/–ø—Ä–∂",
    "–†–∞–¥–∏—É—Å –∫—Ä–∏–≤–æ–π (–ø—Ä—è–º–∞—è)", "–¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞ –≤–æ–∑–¥—É—Ö–∞ ", "–¢–µ–º–ø–µ—Ä–∞—Ç—É—Ä–∞ —Ä–µ–ª—å—Å–∞",
    "–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Ñ–∏–∫—Å–∞—Ü–∏–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏ –º–µ–∂–¥—É –ø—Ä–æ—Ö–æ–¥–∞–º–∏", "–ë–µ—Å—Å—Ç—ã–∫–æ–≤–æ–π, –ó–≤–µ–Ω—å–µ–≤–æ–π",
    "–°—Ä–æ–∫ —É—Å—Ç—Ä–∞–Ω–µ–Ω–∏—è", "–ö—Ç–æ –≤—ã–µ–∑–∂–∞–ª –Ω–∞ –º–µ—Å—Ç–æ —Ä–∏—Ö—Ç–æ–≤–∫–∏ —Å –æ—Å–º–æ—Ç—Ä–æ–º (–¥–æ–ª–∂–Ω–æ—Å—Ç—å, –§–ò–û)",
    "–ü—Ä–∏—á–∏–Ω–∞ –ø–æ—è–≤–ª–µ–Ω–∏—è –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏, –≤—ã–≤–æ–¥—ã. –ü–ª–∞–Ω–æ–≤–∞—è –¥–∞—Ç–∞ —É—Å—Ç—Ä–∞–Ω–µ–Ω–∏—è",
    "–î–∞—Ç–∞ —É—Å—Ç—Ä–∞–Ω–µ–Ω–∏—è (—Ñ.‚Ññ, –≤–∏–¥ —Ä–∞–±–æ—Ç—ã, –¥–∞—Ç–∞, –≤—Ä–µ–º—è, –¥–æ–ª–∂–Ω–æ—Å—Ç—å, –§–ò–û)"
  ]);
  forBrowserPageRenderObj.header.push(
    "‚Ññ", "–î–∞—Ç–∞ –ø—Ä–æ–≤–µ—Ä–∫–∏", "–ü–°", "–ü–ï–†–ï–ì–û–ù, –°–¢–ê–ù–¶–ò–Ø", "–ü–ß",
    "–ü–£–¢–¨, —Å—ä–µ–∑–¥", "–ö–ú", "–ü–ö/–º", "–°—Ç–µ–ø–µ–Ω—å —Ä–∏—Ö—Ç–æ–≤–∫–∏ (—à—Ç)", "–í–µ–ª–∏—á –æ—Ç—Å—Ç—É–ø –º–º/–º",
    "–ò–∑–º–µ–Ω–µ–Ω–∏–µ –≤–µ–ª–∏—á–∏–Ω—ã —Ä–∏—Ö—Ç–æ–≤–∫–∏", "–£—Å—Ç–∞–Ω. –°–∫–æ—Ä–æ—Å—Ç—å", "–û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏—è –°–ö–û–†–û–°–¢–¨ –ø–∞—Å—Å/–≥—Ä—É–∑/–ø—Ä–∂"
  );

  data.forEach((item, i) => {
    
    // —ç—Ç–æ—Ç –º–∞—Å—Å–∏–≤ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–ª—è –ø—É—à–∞ –≤ –Ω–µ–≥–æ –≤—Å–µ—Ö –¥–∞–Ω–Ω—ã—Ö –ø–æ –æ–¥–Ω–æ–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏,
    // —á—Ç–æ–±—ã –ø–æ—Ç–æ–º –ø–æ–ª—É—á–∏—Ç—å –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ –≤—Å–µ—Ö –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ excel —Å –ø–æ–º–æ—â—å—é –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ XLSX;
    const forXLSXArr = [];

    forXLSXArr.push(
      item.sequentialNumber, item.fullDate, item.vagonNumber, item.station,
      item.distanceNumber, item.trackNumber, item.kilometer,
      `${item.picket}/${item.meter}`, item.degree, item.amount,
      item.amountChange, item.advancedSpeed, item.restrictionSpeed,
      "", "", "", "", "", "", "", "", ""
    );   // –º–∞—Å—Å–∏–≤ –æ–¥–Ω–∞ –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å

    // –∑–∞–ø—É—à–∏–º –º–∞—Å—Å–∏–≤ —Å –æ–¥–Ω–æ–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å—é –≤ –º–∞—Å—Å–∏–≤ —Å–æ –≤—Å–µ–º–∏ –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—è–º–∏ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ excel —Å –ø–æ–º–æ—â—å—é –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ XLSX; –ë—É–¥–µ–º –ø–æ—à–∏—Ç—å –∫–∞–∂–¥—É—é –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å
    forXLSXAoA.push(forXLSXArr);




    // —ç—Ç–æ—Ç –º–∞—Å—Å–∏–≤ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–ª—è –ø—É—à–∞ –≤ –Ω–µ–≥–æ –≤—Å–µ—Ö –¥–∞–Ω–Ω—ã—Ö –ø–æ –æ–¥–Ω–æ–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏,
    // —á—Ç–æ–±—ã –ø–æ—Ç–æ–º –ø–æ–ª—É—á–∏—Ç—å –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ –≤—Å–µ—Ö –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏ –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤ –±—Ä–∞—É–∑–µ—Ä–µ
    const forPageBrowserArr = [];

    forPageBrowserArr.push(
      item.sequentialNumber, item.fullDate, item.vagonNumber, item.station,
      item.distanceNumber, item.trackNumber, item.kilometer,
      `${item.picket}/${item.meter}`, item.degree, item.amount,
      item.amountChange, item.advancedSpeed, item.restrictionSpeed
    );   // –º–∞—Å—Å–∏–≤ –æ–¥–Ω–∞ –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å

    // –∑–∞–ø—É—à–∏–º –º–∞—Å—Å–∏–≤ —Å –æ–¥–Ω–æ–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å—é –≤ –º–∞—Å—Å–∏–≤ —Å–æ –≤—Å–µ–º–∏ –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—è–º–∏ –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤ –±—Ä–∞—É–∑–µ—Ä–µ
    forBrowserPageRenderObj.body.push(forPageBrowserArr);
  });

  returnedObj = {
    forXLSXAoA,
    forBrowserPageRenderObj
  };

  return returnedObj;
}