import {
    WAGON_FULL_NAME, INSPECTION_AREA
} from "./actionTypes";

const initialState = {
    wagonFullName: "",          // –ü–æ–ª–Ω–æ–µ –∏–º—è –≤–∞–≥–æ–Ω–∞, –Ω–∞–ø—Ä–∏–º–µ—Ä "–ü–°-025"
    inspectionArea: ""          // –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —É–∫–∞–∑—ã–≤–∞–µ—Ç —É—á–∞—Å—Ç–æ–∫ –ø—Ä–æ–≤–µ—Ä–∫–∏ –æ—Ç —Å—Ç–∞–Ω—Ü–∏–∏ –∫ —Å—Ç–∞–Ω—Ü–∏–∏ –ª—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è –≤ —Ç–µ–ª–µ–≥—Ä–∞–º–º–µ
};

const wagonInfoReducer = (state = initialState, action) => {
    switch (action.type) {

        case WAGON_FULL_NAME: {
            const superState = {
                ...state,
                wagonFullName: action.wagonFullName
            };
            return superState;
        }

        case INSPECTION_AREA: {
            const superState = {
                ...state,
                inspectionArea: action.inspectionArea
            };
            return superState;
        }

        default: return state;
    }
};

export default wagonInfoReducer;