/*
    –§—É–Ω–∫—É–∏—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç DB –∏–∑ —ç—Ç–æ–≥–æ –ø—Ä–æ–µ–∫—Ç–∞ –∏ –ø–æ –∫–æ–¥—É –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è –∏ –∫–∏–ª–æ–º–µ—Ç—Ä—É –æ–ø—Ä–µ–¥–µ–ª—è–µ—Ç –Ω–∞–∑–≤–∞–Ω–∏–µ —Å—Ç–∞–Ω—Ü–∏–∏/–ø–µ—Ä–µ–≥–æ–Ω–∞ –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –Ω–∞—Ö–≤–∞–Ω–∏–µ
*/

export function getStationNameByKmAndDirection(DB, directionCode, doubleKm) {
    // –Ω–∞–π–¥–µ–º –≤ DB –æ–±—ä–µ–∫—Ç —Å –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–µ–π –æ —Å—Ç–∞–Ω—Ü–∏–∏ –∏–ª–∏ –ø–µ—Ä–µ–≥–æ–Ω–µ –≥–¥–µ –º—ã –Ω–∞—Ö–æ–¥–∏–º—Å—è
    const stationObject = DB.stationBoundaries.find(el => {
        const parseDirectionCode = el.direction.match(/\d\d\d\d\d/);         // —Ä–∞—Å–ø–∞—Ä—Å–∏–º –∏–∑ —Å–≤–æ–π—Å—Ç–≤–∞ direction –≤ DB –∫–æ–¥ –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏—è –¥–ª—è —Å—Ä–∞–≤–Ω–µ–Ω–∏—è –µ–≥–æ —Å —Ç–µ–∫—É—â–∏–º –∫–æ–¥–æ–º –≤ item

        return +doubleKm > +el.startCoordinate && +doubleKm <= +el.endCoordinate && +directionCode === +parseDirectionCode[0];
    });
    const stationName = typeof stationObject === 'undefined' ? "" : stationObject.station;  // –Ω–∞–∑–≤–∞–Ω–∏–µ —Å—Ç–∞–Ω—Ü–∏–∏ –∏–ª–∏ –ø–µ—Ä–µ–≥–æ–Ω–∞, –µ—Å–ª–∏ –Ω–µ –Ω–∞—à–ª–∏ –≤ –±–∞–∑–µ —Å–æ–≤–ø–∞–¥–µ–Ω–∏–π, –Ω–µ –Ω–∞—à–ª–∏ –Ω—É–∂–Ω—É—é —Å—Ç–∞–Ω—Ü–∏—é –±—É–¥—É—Ç "", –µ—Å–ª–∏ –Ω–∞—à–ª–∏ –±—É–¥–µ—Ç —Å—Ç–∞–Ω—Ü–∏—è –∏–∑ –±–∞–∑—ã

    return stationName;
}