import {
    setWorkBook3DataActionCreator, setIsWorkBook3DataLoadingActionCreator,
    setIsWorkBook3DataLoadedActionCreator
} from "./actionCreators";


export const setWorkBook3DataThunkCreator = (worBookData) => async (dispatch) => {
    dispatch(setIsWorkBook3DataLoadingActionCreator(true));                              // –∑–∞–ø–∏—à–µ–º –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ –º—ã –∑–∞–≥—Ä—É–∂–∞–µ–º –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ–±—ã –ø–æ–∫–∞—Ö—ã–≤–∞—Ç—å –ª–æ–∞–¥–µ—Ä

    dispatch(setWorkBook3DataActionCreator(worBookData));                                // –∑–∞–≥—Ä—É–∑–∏–º –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç

    dispatch(setIsWorkBook3DataLoadingActionCreator(false));                             // –∑–∞–ø–∏—à–µ–º –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ –º—ã —É–∂–µ –∑–∞–≥—Ä—É–∑–∏–ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç, —á—Ç–æ–±—ã —É–±—Ä–∞—Ç—å –ª–æ–∞–¥–µ—Ä

    dispatch(setIsWorkBook3DataLoadedActionCreator(true));                               // –¥–∞–Ω–Ω—ã–µ –∑–∞–≥—Ä—É–∂–µ–Ω—ã –≤ —Å—Ç–µ–π—Ç
}