/* –§—É–Ω–∫—Ü–∏—è –ø–æ–ª—É—á–∞–µ—Ç –¥–∞–Ω–Ω—ã–µ - —Ç–∏–ø –∫–∞–∫ –≤ —Å—Ç–µ–π—Ç–µ, –ø—Ä–µ–æ–±—Ä–∞–∑—É–µ—Ç –∏—Ö –≤ –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ –¥–ª—è –∑–∞–ø–∏—Å–∏ –≤ –∫–Ω–∏–≥—É –∏ –ø—Ä–µ–¥–ª–∞–≥–∞–µ—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é —ç—Ç—É –∫–Ω–∏–≥—É —Å–∫–∞—á–∞—Ç—å */
import XLSX from "xlsx/dist/xlsx.full.min";

/*
    –ø—Ä–∏–Ω–∏–º–∞–µ–º—ã–µ –ø–∞—Ä–∞–º–µ—Ç—Ä—ã:
    data - –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤ - —Ç–∏–ø –∫–∞–∫ –≤ —Å—Ç–µ–π—Ç–µ,
    bookName - –Ω–∞–∑–≤–∞–Ω–∏–µ —Å–æ–∑–¥–∞–≤–∞–µ–º–æ–π –∫–Ω–∏–≥–∏,
    sheetName - –Ω–∞–∑–≤–∞–Ω–∏–µ —Å–æ–∑–¥–∞–≤–∞–µ–º–æ–≥–æ –ª–∏—Å—Ç–∞
*/

export function createAndUploadWorkBook(data, bookName, sheetName) {
    if(!Array.isArray(data)) {
        console.error("–ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–µ –í–∞–º–∏ –¥–∞–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –≤ —Ñ—É–Ω–∫—Ü–∏—é creatrAndUploadWorkBook() –Ω–µ —è–≤–ª—é—Ç—Å—è –º–∞—Å—Å–∏–≤–æ–º");
        return alert ("–ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–µ –í–∞–º–∏ –¥–∞–Ω–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –≤ —Ñ—É–Ω–∫—Ü–∏—é creatrAndUploadWorkBook() –Ω–µ —è–≤–ª—é—Ç—Å—è –º–∞—Å—Å–∏–≤–æ–º");
    }

    const wb = XLSX.utils.book_new();                                   // —Å–æ–∑—ã–¥–∞–¥–∏–º –Ω–æ–≤—É—é –ø—É—Å—Ç—É—é –∫–Ω–∏–≥—É

    const ws = XLSX.utils.aoa_to_sheet(data);                    // —Å–æ–∑–¥–∞–¥–∏–º –ª–∏—Å—Ç

    XLSX.utils.book_append_sheet(wb, ws, sheetName);                    // –¥–æ–±–∞–≤–∏–º –≤ —Å–æ–∑–¥–∞–Ω–Ω—É—é –∫–Ω–∏–≥—É –ª–∏—Å—Ç

    XLSX.writeFile(wb, bookName);                                       // –∑–∞–ø–∏—à–µ–º —Ñ–∞–π–ª xlsx –∏ –ø–µ—Ä–µ–¥–∞–¥–∏–º –µ–≥–æ –¥–ª—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é
}