import React from "react";
import { Form, Input } from 'antd';
import { selectInspectionArea } from "../../../../state/features/wagonInfo/selectors";
import { setInspectionAreaActionCreator } from "../../../../state/features/wagonInfo/actionCreators";
import { useDispatch, useSelector } from "react-redux";

/* –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —É–∫–∞–∑—ã–≤–∞–µ—Ç —É—á–∞—Å—Ç–æ–∫ –ø—Ä–æ–≤–µ—Ä–∫–∏ –æ—Ç —Å—Ç–∞–Ω—Ü–∏–∏ –∫ —Å—Ç–∞–Ω—Ü–∏–∏ */

export const GeneralSettingsInspectionArea = () => {
    // -------------------------------------------------------------- –•—É–∫–∏ ---------------------------------------------------------------------------
    const dispatch = useDispatch();
    const inspectionArea = useSelector(selectInspectionArea);
    // -------------------------------------------------------------- / –•—É–∫–∏ -------------------------------------------------------------------------

    // ------------------------------------ Declare —Ñ—É–Ω–∫—Ü–∏—é –≤—ã–∑—ã–≤–∞—é—â—É—é—Å—è –ø—Ä–∏ –≤–≤–æ–¥–µ —Ç–µ–∫—Å—Ç–∞ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –∏–º–µ–Ω–∏ –≤–∞–≥–æ–Ω–∞   ------------------------------------------------
    const onInputChange = (value) => {
        dispatch(setInspectionAreaActionCreator(value));                   // –∑–∞–ø–∏—à–µ–º –≤–≤–µ–¥–µ–Ω–Ω–æ–µ –∏–º—è –≤–∞–≥–æ–Ω–∞ –≤ —Å—Ç–µ–π—Ç
    }
    // ------------------------------------ / Declare —Ñ—É–Ω–∫—Ü–∏—é –≤—ã–∑—ã–≤–∞—é—â—É—é—Å—è –ø—Ä–∏ –≤–≤–æ–¥–µ —Ç–µ–∫—Å—Ç–∞ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –∏–º–µ–Ω–∏ –≤–∞–≥–æ–Ω–∞  ---------------------------------------------


    return (
        <>
            <div className="content__input-item">
                <Form layout="inline">
                    <Form.Item label="–í–µ–¥–∏—Ç–µ —É—á–∞—Å—Ç–æ–∫ –ø—Ä–æ–≤–µ—Ä–∫–∏">
                        <Input placeholder="–£—á–∞—Å—Ç–æ–∫ –ø—Ä–æ–≤–µ—Ä–∫–∏" value={inspectionArea} onChange={(e) => onInputChange(e.target.value)} />
                    </Form.Item>
                </Form>
            </div>

        </>
    );
}