import {
    VIDEO_BOOK_DATA, IS_VIDEO_BOOK_DATA_LOADED
} from "./actionTypes";

/* –≤ videoBookDataObject –ª–µ–∂–∞—Ç –¥–∞–Ω–Ω—ã–µ –∏–∑ –∫–Ω–∏–≥–∏ –≤—ã–≥—Ä—É–∂–µ–Ω–Ω–æ–π –∏–∑ –ø—Ä–æ–≥—Ä–∞–º–º—ã –ê–†–ú-–í–∏–¥–µ–æ–∫–æ–Ω—Ç—Ä–æ–ª—å —Å –ø–æ–º–æ—â—å—é "ctrl+e",
—Å—Ç—Ä—É–∫—Ç—É—Ä–∞ –≤–Ω—É—Ç—Ä–∏ –∫–∞–∂–¥–æ–≥–æ –∏–∑ —ç—Ç–∏—Ö –æ–±—ä–µ–∫—Ç–æ–≤: –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤(–æ–¥–∏–Ω –æ–±—ä–µ–∫—Ç === –æ–¥–Ω–∞ —Å—Ç—Ä–æ–∫–∞ –≤ –µ–∫—Å–µ–ª—å) */
export const setVideoBookDataActionCreator = ( videoBookDataObject ) => {
    return {
        type: VIDEO_BOOK_DATA,
        videoBookDataObject
    }
};

export const setIsVideoBookDataLoadedActionCreator = ( boolVar ) => {                // –∑–∞–≥—Ä—É–∑–∏–ª–∏—Å—å –ª–∏ –¥–∞–Ω–Ω—ã–µ –≤ —Å—Ç–µ–π—Ç
    return {
        type: IS_VIDEO_BOOK_DATA_LOADED,
        isVideoBookDataLoaded: boolVar
    }
};