/* —Ñ—É–Ω–∫—Ü–∏—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –º–∞—Å—Å–∏–≤ –æ–±—ä–µ–∫—Ç–æ–≤ 3 –∏ 4 —Å—Ç–µ–ø–µ–Ω–µ–π (—Ç–∏–ø –∫–∞–∫ –≤ —Å—Ç–µ–π—Ç–µ)
    –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ 3 –∏ 4 —Å—Ç–µ–ø–µ–Ω–µ–π
*/
import { definePicketByMeter } from "../../../common/definePicketByMeter/definePicketByMeter";
import DB from "../../../../DB/DB";
import { sheetOtstConst } from "../../../../CONSTS/sheetsHeaderConsts";
import { getStationNameByKmAndDirection } from "../../../common/getStationNameByKmAndDirection/getStationNameByKmAndDirection";

export function createThirdAndFourthDegreesAoA(data) {
  // –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—ã–π –æ–±—ä–µ–∫—Ç, —Ç—É—Ç –±—É–¥–µ—Ç 1 –º–∞—Å—Å–∏–≤ –∏ 1 –æ–±—ä–µ–∫—Ç:
  // 1 –º–∞—Å—Å–∏–≤ - –º–∞—Å—Å–æ–≤ –º–∞—Å—Å–≤–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ excel —Å –ø–æ–º–æ—â—å—é –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ XLSX;
  // 2 –æ–±—ä–µ–∫—Ç - –æ–±—ä–µ–∫—Ç –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤ –±—Ä–∞—É–∑–µ—Ä–µ, —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ 2 —Å–≤–æ–π—Å—Ç–≤:
  // 1 —Å–≤–æ–π—Å—Ç–≤–æ - –º–∞—Å—Å–∏–≤ –∏–∑ —ç–ª–µ–º–Ω—Ç–æ–≤ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è header`–∞ —Ç–±–∞–ª–∏—Ü–∞,
  // 2 —Å–≤–æ–π—Å—Ç–≤–æ –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è —Ç–µ–ª–∞ —Ç–∞–±–ª–∏—Ü—ã.
  let returnedObj = {};

  // –º–∞—Å—Å–æ–≤ –º–∞—Å—Å–≤–æ–≤ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ excel —Å –ø–æ–º–æ—â—å—é –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ XLSX;
  let forXLSXAoA = [];

  // –æ–±—ä–µ–∫—Ç –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤ –±—Ä–∞—É–∑–µ—Ä–µ, —Å–æ—Å—Ç–æ–∏—Ç –∏–∑ 2 —Å–≤–æ–π—Å—Ç–≤:
  let forBrowserPageRenderObj = {
    header: [],         // 1 —Å–≤–æ–π—Å—Ç–≤–æ - –º–∞—Å—Å–∏–≤ –∏–∑ —ç–ª–µ–º–Ω—Ç–æ–≤ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è header`–∞ —Ç–±–∞–ª–∏—Ü–∞,
    body: []            // 2 —Å–≤–æ–π—Å—Ç–≤–æ –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ —Å –¥–∞–Ω–Ω—ã–º–∏ –¥–ª—è —Å–æ–∑–¥–∞–Ω–∏—è —Ç–µ–ª–∞ —Ç–∞–±–ª–∏—Ü—ã.
  };

  // –®–∞–ø–∫–∞ —Ç–∞–±–ª–∏—Ü—ã
  forXLSXAoA.push([
    "–ü–°", "‚Ññ", "–ü–ß", "–ü–µ—Ä–µ–≥–æ–Ω", "–ü–£–¢–¨", "KM", "–ü–ö/–º", "–°–ö–û–†–û–°–¢–¨ —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω–∞—è", "–°–ö–û–†–û–°–¢–¨* –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏—è –ø–∞—Å—Å/–≥—Ä—É–∑.",
    "–í—Ä–µ–º—è –≤—ã–¥–∞—á–∏ –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏—è", "–°—Ç–µ–ø–µ–Ω—å –æ—Ç—Å—Ç.", "–ü–†–ò–ß–ò–ù–ê", "–ù–∞–ª–∏—á–∏–µ –ø–æ–≤—Ç–æ—Ä–∞", "–£–°–¢–†–ê–ù–ï–ù–ò–ï", "–£–°–¢–†–ê–ù–ï–ù–ò–ï –ü–†–û–í–ï–†–ò–õ"
  ]);
  forBrowserPageRenderObj.header.push(
    "–ü–°", "‚Ññ", "–ü–ß", "–ü–µ—Ä–µ–≥–æ–Ω", "–ü–£–¢–¨", "KM", "–ü–ö/–º", "–°–ö–û–†–û–°–¢–¨ —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω–∞—è", "–°–ö–û–†–û–°–¢–¨* –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏—è –ø–∞—Å—Å/–≥—Ä—É–∑.",
    "–°—Ç–µ–ø–µ–Ω—å –æ—Ç—Å—Ç.", "–ü–†–ò–ß–ò–ù–ê"
  );

  data.forEach((item, i) => {
    // —ç—Ç–æ—Ç –º–∞—Å—Å–∏–≤ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–ª—è –ø—É—à–∞ –≤ –Ω–µ–≥–æ –≤—Å–µ—Ö –¥–∞–Ω–Ω—ã—Ö –ø–æ –æ–¥–Ω–æ–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏,
    // —á—Ç–æ–±—ã –ø–æ—Ç–æ–º –ø–æ–ª—É—á–∏—Ç—å –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ –≤—Å–µ—Ö –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ excel —Å –ø–æ–º–æ—â—å—é –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ XLSX;
    const forXLSXArr = [];

    const pkMetr = definePicketByMeter(item[sheetOtstConst.METER]) + "/" + item[sheetOtstConst.METER];      // –ø–∏–∫–µ—Ç –∏ –º–µ—Ç—Ä –≤ —Ñ–æ—Ä–º–∞—Ç–µ "5/214"
    const setSpeed = item[sheetOtstConst.PASSENGER_SPEED_ADVANCED] + "/" + item[sheetOtstConst.FREIGHT_SPEED_ADVANCED];     // —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–Ω–∞—è —Å–∫–æ—Ä–æ—Å—Ç—å –≤ —Ñ–æ—Ä–º–∞—Ç–µ "80/80"

    // ------- —Å–∫–æ—Ä–æ—Å—Ç—å –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏—è ------
    let limitingSpeed;
    if (item[sheetOtstConst.PASSENGER_SPEED_RESTRICTION] === "-") {
      if (item[sheetOtstConst.FREIGHT_SPEED_RESTRICTION] === "-") {
        limitingSpeed = "";
      } else {
        limitingSpeed = item[sheetOtstConst.PASSENGER_SPEED_RESTRICTION] + "/" + item[sheetOtstConst.FREIGHT_SPEED_RESTRICTION]
      }
    } else {
      limitingSpeed = item[sheetOtstConst.PASSENGER_SPEED_RESTRICTION] + "/" + item[sheetOtstConst.FREIGHT_SPEED_RESTRICTION]
    }
    // ------- / —Å–∫–æ—Ä–æ—Å—Ç—å –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏—è ----

    const faultDecoding = item[sheetOtstConst.RETREAT_TITLE] + " " + item[sheetOtstConst.AMPLITUDE] + "/" + item[sheetOtstConst.LENGTH_OF_RETREAT];  // –ø—Ä–∏—á–∏–Ω–∞ –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏ –≤ —Ñ–æ—Ä–º–∞—Ç–µ "–ü 16/12"


    const doubleKm = Number(item[sheetOtstConst.KILOMETER] + "." + item[sheetOtstConst.METER]);     // –∫–∏–ª–æ–º–µ—Ç—Ä –∏ –º–µ—Ç—Ä –ø—Ä–∏–≤–µ–¥–µ–º –∫ –≤–∏–¥—É 132.456

    const stationName = getStationNameByKmAndDirection(DB, item[sheetOtstConst.DIRECTION_CODE], doubleKm);    // –°—Ç–∞–Ω—Ü–∏—è –∏–ª–∏ –ø–µ—Ä–µ–≥–æ–Ω –æ–ø—Ä–µ–¥–µ–ª–∏–º –∏–∑ DB

    // --------------------- –≠–¢–û –í–†–ï–ú–ï–ù–ù–´–ô –ö–û–°–¢–´–õ–¨ –î–õ–Ø –§–ò–õ–¨–¢–†–ê–¶–ò–ò –ù–ï–ò–°–ü–†–ê–í–ù–û–°–¢–ï–ô –¢–†–ï–ë–£–Æ–©–ò–• –ö–û–†–†–ï–ö–¢–ò–†–û–í–û–í–ö --------------------------------------
    let isCorrectionNeed = null;
    if (item[sheetOtstConst.RETREAT_TITLE] === "–ü" && item[sheetOtstConst.AMPLITUDE] <= 15) isCorrectionNeed = "–¢—Ä–µ–±—É–µ—Ç –∫–æ—Ä—Ä–µ–∫—Ç–∏—Ä–æ–≤–∫–∏ —Å–æ–≥–ª–∞—Å–Ω–æ —Ä–∞—Å–ø–æ—Ä—è–∂–Ω–∏—é ‚Ññ 614"
    // --------------------- / –≠–¢–û –í–†–ï–ú–ï–ù–ù–´–ô –ö–û–°–¢–´–õ–¨ –î–õ–Ø –§–ò–õ–¨–¢–†–ê–¶–ò–ò –ù–ï–ò–°–ü–†–ê–í–ù–û–°–¢–ï–ô –¢–†–ï–ë–£–Æ–©–ò–• –ö–û–†–†–ï–ö–¢–ò–†–û–í–û–í–ö ------------------------------------


    forXLSXArr.push(
      item[sheetOtstConst.WAGON_NUMBER], ++i, item[sheetOtstConst.RAILWAY_DISTANCE],
      stationName, item[sheetOtstConst.TRACK], item[sheetOtstConst.KILOMETER],
      pkMetr, setSpeed, limitingSpeed, "", item[sheetOtstConst.DEGREE],
      faultDecoding, isCorrectionNeed, "", ""
    );   // –º–∞—Å—Å–∏–≤ –æ–¥–Ω–∞ –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å
    // –∑–∞–ø—É—à–∏–º –º–∞—Å—Å–∏–≤ —Å –æ–¥–Ω–æ–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å—é –≤ –º–∞—Å—Å–∏–≤ —Å–æ –≤—Å–µ–º–∏ –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—è–º–∏ –¥–ª—è —Ñ–æ—Ä–º–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–∏–≥–∏ excel —Å –ø–æ–º–æ—â—å—é –±–∏–±–ª–∏–æ—Ç–µ–∫–∏ XLSX; –ë—É–¥–µ–º –ø–æ—à–∏—Ç—å –∫–∞–∂–¥—É—é –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å
    forXLSXAoA.push(forXLSXArr);



    // —ç—Ç–æ—Ç –º–∞—Å—Å–∏–≤ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –¥–ª—è –ø—É—à–∞ –≤ –Ω–µ–≥–æ –≤—Å–µ—Ö –¥–∞–Ω–Ω—ã—Ö –ø–æ –æ–¥–Ω–æ–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏,
    // —á—Ç–æ–±—ã –ø–æ—Ç–æ–º –ø–æ–ª—É—á–∏—Ç—å –º–∞—Å—Å–∏–≤ –º–∞—Å—Å–∏–≤–æ–≤ –≤—Å–µ—Ö –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç–∏ –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤ –±—Ä–∞—É–∑–µ—Ä–µ
    const forPageBrowserArr = [];

    forPageBrowserArr.push(
      item[sheetOtstConst.WAGON_NUMBER], i, item[sheetOtstConst.RAILWAY_DISTANCE],
      stationName, item[sheetOtstConst.TRACK], item[sheetOtstConst.KILOMETER],
      pkMetr, setSpeed, limitingSpeed, item[sheetOtstConst.DEGREE],
      faultDecoding, isCorrectionNeed
    );   // –º–∞—Å—Å–∏–≤ –æ–¥–Ω–∞ –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å

    // –∑–∞–ø—É—à–∏–º –º–∞—Å—Å–∏–≤ —Å –æ–¥–Ω–æ–π –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—å—é –≤ –º–∞—Å—Å–∏–≤ —Å–æ –≤—Å–µ–º–∏ –Ω–µ–∏—Å–ø—Ä–∞–≤–Ω–æ—Å—Ç—è–º–∏ –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏ —Ç–∞–±–ª–∏—Ü—ã –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü–µ –≤ –±—Ä–∞—É–∑–µ—Ä–µ
    forBrowserPageRenderObj.body.push(forPageBrowserArr);
  });

  returnedObj = {
    forXLSXAoA,
    forBrowserPageRenderObj
  };

  return returnedObj;
}