import React from "react";
import { Form, Input } from 'antd';
import { selectWagonFullName } from "../../../../state/features/wagonInfo/selectors";
import { setWagonFullNameActionCreator } from "../../../../state/features/wagonInfo/actionCreators";
import { useDispatch, useSelector } from "react-redux";

/* –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —É–∫–∞–∑—ã–≤–∞–µ—Ç –ø–æ–ª–Ω–æ–µ –∏–º—è –ú–°–î –¥–ª—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è –≤ —Ç–µ–ª–µ–≥—Ä–∞–º–º–µ */

export const GeneralSettingsWagonNameInputForm = () => {
    // -------------------------------------------------------------- –•—É–∫–∏ ---------------------------------------------------------------------------
    const dispatch = useDispatch();
    const wagonFullName = useSelector(selectWagonFullName);
    // -------------------------------------------------------------- / –•—É–∫–∏ -------------------------------------------------------------------------

    // ------------------------------------ Declare —Ñ—É–Ω–∫—Ü–∏—é –≤—ã–∑—ã–≤–∞—é—â—É—é—Å—è –ø—Ä–∏ –≤–≤–æ–¥–µ —Ç–µ–∫—Å—Ç–∞ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –∏–º–µ–Ω–∏ –≤–∞–≥–æ–Ω–∞   ------------------------------------------------
    const onInputChange = (value) => {
        dispatch(setWagonFullNameActionCreator(value));                   // –∑–∞–ø–∏—à–µ–º –≤–≤–µ–¥–µ–Ω–Ω–æ–µ –∏–º—è –≤–∞–≥–æ–Ω–∞ –≤ —Å—Ç–µ–π—Ç
    }
    // ------------------------------------ / Declare —Ñ—É–Ω–∫—Ü–∏—é –≤—ã–∑—ã–≤–∞—é—â—É—é—Å—è –ø—Ä–∏ –≤–≤–æ–¥–µ —Ç–µ–∫—Å—Ç–∞ –≤ –ø–æ–ª–µ –≤–≤–æ–¥–∞ –∏–º–µ–Ω–∏ –≤–∞–≥–æ–Ω–∞  ---------------------------------------------


    return (
        <>
            <div className="content__input-item">
                <Form layout="inline">
                    <Form.Item label="–í–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –≤–∞–≥–æ–Ω–∞ –¥–ª—è –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏—è –≤ —Ç–µ–ª–µ–≥—Ä–∞–º–º–µ">
                        <Input placeholder="–ù–∞–∑–≤–∞–Ω–∏–µ –≤–∞–≥–æ–Ω–∞" value={wagonFullName} onChange={(e) => onInputChange(e.target.value)} />
                    </Form.Item>
                </Form>
            </div>

        </>
    );
}