/* –§—É–Ω–∫—Ü–∏—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç —Ç–∏–ø –ø—Ä–æ–≤–µ—Ä–∫–∏ —Ü–∏—Ñ—Ä–æ–π –∏ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –±—É–∫–≤–∞–º–∏ –Ω–∞–ø—Ä–∏–º–µ—Ä "–†–∞–±–æ—á–∞—è" */

export function defineTypeOfCheckNameByTypeOfChekNumber(typeOfCheckNumber) {
    let typeOfCheckString = "";                                     // –í–∏–¥ –ø—Ä–æ–≤–µ—Ä–∫–∏ –±—É–∫–≤–∞–º–∏

    if (!Number.isFinite(typeOfCheckNumber)) {                     // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ
        typeOfCheckNumber = +typeOfCheckNumber;                         // –ø—Ä–∏–≤–µ–¥–µ–º –µ–≥–æ –∫ —á–∏—Å–ª—É
    }

    if (Number.isNaN(typeOfCheckNumber)) {                         // –µ—Å–ª–∏ –ø–µ—Ä–µ–¥–∞–Ω–æ (–∏–ª–∏ –ø–æ—Å–ª–µ –ø–µ—Ä–≤–æ–≥–æ –ø—Ä–∏–≤–µ–¥–µ–Ω–∏—è –∫ —á–∏—Å–ª—É) NaN
        alert("–≤ —Ñ—É–Ω–∫—Ü–∏—é –æ–ø—Ä–µ–¥–µ–ª—è—é—â—É—é —Ç–∏–ø –ø—Ä–æ–≤–µ—Ä–∫–∏ –±—É–∫–≤–∞–º–∏ –ø–æ —Ç–∏–ø—É –ø—Ä–æ–≤–µ—Ä–∫–∞–º–∏ —Ü–∏—Ñ—Ä–æ–π –ø–µ—Ä–µ–¥–∞–Ω–æ –Ω–µ —á–∏—Å–ª–æ");   // —Å–æ–æ–±—â–µ–Ω–∏–µ –æ–± –æ—à–∏–±–∫–µ
    }

    if(typeOfCheckNumber === 0) {
        typeOfCheckString = "–†–∞–±–æ—á–∞—è";
    } else if(typeOfCheckNumber === 1) {
        typeOfCheckString = "–ö–æ–Ω—Ç—Ä–æ–ª—å–Ω–∞—è";
    } else if(typeOfCheckNumber === 2) {
        typeOfCheckString = "–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω–∞—è";
    } else {
        console.error(`–í –§—É–Ω–∫—Ü–∏—é defineTypeOfCheckNameByTypeOfChekNumber –ø–µ—Ä–µ–¥–∞–Ω –Ω–æ–º–µ—Ä –≤–∏–¥–∞ –ø—Ä–æ–≤–µ—Ä–∫–∏ ${typeOfCheckNumber}`);
        typeOfCheckString = "";
    }

    return typeOfCheckString;
}